/// <reference path="ZUI.ts"/>

class ColorPickerUI extends ZUI {
    getF_:Fstring;
    setF_:StringSetF;
    constructor(){
        super();
        this.getF_=null;
        this.setF_=null;
    }
    getF(getFF:Fstring):ColorPickerUI{ 
        this.getF_=getFF;
        return this;
    }
    setF(setFF:StringSetF):ColorPickerUI{
        this.setF_=setFF;
        return this;
    }
    renderJQ():JQuery {
        let val = ZUI.stringVal(this.getF_);
        if (val=="none")
            val="#FFFFFF";
        let jq = $(`<input type='color' class='ColorPickerUI ${this.classStr()}' value='${val}'/>`);

        jq.on("change",(event)=>{
            let newVal = jq.val();
            if (this.setF_){
                this.setF_(newVal);
            }
        })
        this.applyCSS(jq);
        return jq;
    }
}