/// <reference path="ServerContext.ts"/>
/// <reference path="./DataSource.ts"/>


//
class DataObj {
	protected _type:string;
	_key:string;
	parent:DataObj;
	dataSource: DataSource;
	static globalSource:DataSource;
	private blockTouch_:boolean;
	constructor (json:any){
		this.blockTouch_=false;
		this._type = this.getTypeCode();
		this._key = json._key;
		if (!json._key && json._id){
			this._key = this.getTypeCode()+"_"+json._id;
		}
		this.parent=null;
	}
	
	getTypeCode():string{
		return null;
	}
	getDataSource():DataSource{
		if (this.dataSource)
			return this.dataSource;
		else if (this.parent)
			return this.parent.getDataSource();
		else if (DataObj.globalSource)
			return DataObj.globalSource;
		else
			return null;
	}
	getUserManager():UserManager{
		let ds = this.getDataSource();
		if (ds){
			let rm = ds.getRightsManager();
			if (rm){
				let um = rm.userManager;
				return um;
			}
		}
		return null;
	}
	touch():boolean{ // touch will return false if any permissions are violated.
		if (this.blockTouch_) return true;
		if (this.dataSource){
			if (this.dataSource.touched(this)){ // the dataSource is the prime mechanism for permissions
				if (this.parent)
					return this.parent.touch();
			} else {
				return false;
			}
		} else {
			if (this.parent)
				return this.parent.touch();
			else
				return true;
		}
	}
	blockTouch(on:boolean){
		this.blockTouch_=on;
		if(!on) this.touch();
	}
	toJSON():any{
        return null;
	}
	static GET(key:string, done:(err:string,newObj:DataObj)=>void,forceRequest:boolean,
			serverContext:ServerContext){
		let ds = DataObj.globalSource;
		if (ds){
			ds.canGet(key,serverContext,(granted:boolean)=>{
				if (granted){
					ds.GET(key,done,forceRequest,serverContext);
				} else {
					done(`ERR GET on ${key} had rights fail`,null);
				}
			});
		} else {
			done(`ERR GET on ${key} has no dataSource`,null);
		}
	}
	static cGET(key:string,forceRequest:boolean){
		let ds=DataObj.globalSource;
		if (ds){
			return ds.cGET(key,forceRequest);
		} else {
			return null;
		}
	}
	static GETm(keys:string[], done:(err:string,newObjs:DataObj[])=>void,forceRequest:boolean,
			serverContext:ServerContext){
		let ds = DataObj.globalSource;
		if (ds){
			ds.canGETm(keys,serverContext,(granted:boolean)=>{
				if (granted){
					ds.GETm(keys,done,forceRequest,serverContext);
				} else {
					done(`ERR GETm  had rights fail`,null);
				}
			});
		} else {
			done(`ERR GETm has no dataSource`,null);
		}
	}
	
	static cGETm(keys:string[],forceRequest?:boolean):DataObj[]{
		let ds= DataObj.globalSource;
		if (ds){
			return ds.cGETm(keys,forceRequest);
		} else {
			return null;
		}
	}
	GETFileBlob(blobKey:string,serverContext:ServerContext,done:(err:string,blob:BlobStoreRec)=>void){
		let ds = this.getDataSource();
		if (ds){
			ds.canGETBlob(blobKey,serverContext,(granted:boolean)=>{
				if (granted){
					ds.GETBlob(blobKey,serverContext,done);
				} else {
					done("ERR GETBlobFile had rights fail",null);
				}
			});
		}
	}
	PUT(done:(err:string,newObj:DataObj)=>void,serverContext?:ServerContext){
		let ds = this.getDataSource();
		if (ds){
			ds.canPUT(this,serverContext,(granted:boolean)=>{
				if (granted){
					ds.PUT(this,done,serverContext);
				} else {
					done(`ERR PUT on ${this._key} had rights fail`,null);
				}
			});
		} else {
			done(`ERR PUT on ${this._key} has no dataSource`,null);
		}
	}
	static PUTFileBlob(file:File,done:(err:string,blobKey:string)=>void,serverContext?:ServerContext){
		let ds=this.globalSource;
		if (ds){
			ds.canPUTBlob(serverContext,(granted:boolean)=>{
				if (granted){
					ds.PUTFileBlob(file,serverContext,done);
				} else {
					done(`ERR PUTFileBlob  had rights fail`,null);
				}
			})
		} else {
			done(`ERR PUTFileBlob has no dataSource`,null);
		}
	}
	static PUTBlob(blob:Blob,name:string,done:(err:string,blobKey:string)=>void,serverContext?:ServerContext){
		let ds=this.globalSource;
		if (ds){
			ds.canPUTBlob(serverContext,(granted:boolean)=>{
				if (granted){
					ds.PUTBlob(blob,name,serverContext,done);
				} else {
					done(`ERR PUTFileBlob  had rights fail`,null);
				}
			})
		} else {
			done(`ERR PUTBlob has no dataSource`,null);
		}		
	}
	static GETBlob(blobKey:string,serverContext:ServerContext,done:(err:string,blob:BlobStoreRec)=>void){
		let ds=this.globalSource;
		if (ds){
			ds.canGETBlob(blobKey,serverContext,(granted:boolean)=>{
				if (granted){
					ds.GETBlob(blobKey,serverContext,done);
				} else {
					done(`ERR getFileBlob had rights fail`,null);
				}
			})
		}
    }
	static PUTm(objs:DataObj[],done:(err:string, newObjs:DataObj[])=>void,serverContext:ServerContext){
		let ds = this.globalSource;
		if (ds){
			ds.canPUTm(objs,serverContext,(granted:boolean)=>{
				if (granted){
					ds.PUTm(objs,done,serverContext);
				} else {
					done(`ERR PUTm had rights fail`,null);
				}
			});
		}else {
			done(`ERR PUTm  has no DataObj.globalSource`,null);
		}
	}
	DELETE(done?:(err:string)=>void,serverContext?:ServerContext){
		let ds = this.getDataSource();
		if (ds){
			ds.canDELETE(this._key,serverContext,(granted:boolean)=>{
				if (granted){
					ds.DELETE(this._key,done);
				} else {
					done(`ERR DELETE on ${this._key} has rights fail`);
				}
			})	
		} else {
			done(`ERR DELETE on ${this._key} has no dataSource`);
		}
	}
	static FIND(typeCode:string,search:Query,done:(err:string,keys:string[])=>void,
			keysOnly:boolean,forceRequest:boolean,serverContext:ServerContext){
		let ds=DataObj.globalSource;
		if (ds){
			ds.canFIND(typeCode,search,serverContext,(granted:boolean)=>{
				if (granted){
					ds.FIND(typeCode,search,done,keysOnly,forceRequest,serverContext);
				} else {
					done(`ERR FIND on ${typeCode} had rights fail`,null);
				}
			})
		} else {
			done(`ERR FIND on ${typeCode} has no dataSource`,null);
		}
	}
	static cFIND(typeCode:string,search:Query,keysOnly:boolean,forceRequest:boolean):string[]{
		let ds= DataObj.globalSource;
		if (ds){
			return ds.cFIND(typeCode,search,keysOnly,forceRequest);
		} else {
			return null;
		}
	}
	static checkTypeCode(expectedType:string, receivedType:string):boolean{
		if (!expectedType) return true;
		if (!receivedType) return true;
		return DataObj.canA[expectedType][receivedType];
	}
	static make:(expectedType:string,json:any)=>DataObj;
	static makeList:(expectedType:string,json:any[],parent:DataObj)=>DataObj[];
	static makeDict:(expectedType:string,json:any,parent:DataObj)=>{[k:string]:DataObj};
	private static classInfo_:DataObjClassInfo;
	static set classInfo(info:DataObjClassInfo){
		DataObj.classInfo_=info;
		DataObj.buildTypeToCollection();
		DataObj.buildCanAssign();
	}
	static get classInfo():DataObjClassInfo{
		return DataObj.classInfo_;
	}
	private static tToC:{ [typeCode:string]:string};
	private static canA:{ [superCode:string]:{[typeCode:string]:boolean}};
	private static sRoots: { [rootClassName:string]:boolean}=null;
	private static buildTypeToCollection(){
		DataObj.tToC={};
		DataObj.sRoots={};
		for ( let typeCode in DataObj.classInfo_){
			DataObj.tToC[typeCode]=DataObj.topSuperCode(typeCode);
		}
	}
	static typeCodeToName(typeCode:string){
		let ci = this.classInfo[typeCode];
		let cn = ci.className;
		return cn;
	}
	static typeToCollection(typeCode:string){
		return DataObj.tToC[typeCode];
	}
	static collections():{ [rootClassName:string]:boolean }{
		return DataObj.sRoots;
	}
	private static topSuperCode(typeCode:string):string{
		let ci = DataObj.classInfo_[typeCode];
		if (ci && ci.superCode && ci.superCode.length>0 && typeCode != ci.superCode){
			return this.topSuperCode(ci.superCode);
		} else {
			DataObj.sRoots[typeCode]=true;
			return typeCode;
		}
	}
	private static buildCanAssign(){
		DataObj.canA = {};
		for (let typeCode in DataObj.classInfo_){
			DataObj.superCanAssign(typeCode,typeCode);
		}
	}
	private static superCanAssign(superCode,typeCode){
		let ca = DataObj.canA;
		if (!ca[superCode])
			ca[superCode]={};
		ca[superCode][typeCode]=true;
		let ci = DataObj.classInfo_[superCode];
		if (ci){
			let sc = ci.superCode;
			if (sc && sc.length>0)
				this.superCanAssign(sc,typeCode);
		}
	}

}

type DataObjClassInfo = { 
	[typeCode:string]:{ 
		className:string, 
		superCode?:string,
		keyed:boolean,
		fields: {
			[fieldName:string]: { type:string, isList:boolean, isDict:boolean}
		}
	}
}

type MongoBinary = any;
type BlobStoreRec = {
	_key?:string,
	name:string,
	type:string,
	nextBlob?:string,
	data: MongoBinary
}