
/// <reference path="typings/jquery.d.ts"/>
/// <reference path="./ZUITypes.ts"/>
/// <reference path="../data/DataObj.ts"/>

class ZUI {
    protected content:ZUI;
    renderJQ():JQuery{
        //return this.content.style(this.classStr()).renderJQ();
        let jq = this.content.renderJQ();
        let classes = this.classStr();
        jq.addClass(classes);
        if (this.css_){
            jq.attr('style',ZUI.stringVal(this.css_))
        }
        return jq;
    }
    static pageManager:PageManager;
    constructor(){
    }
    static notifyCount=0;
    static notify(){ 
        if (this.notifyCount<3){
            this.notifyCount+=1;
            try {
                if (ZUI.pageManager)
                    ZUI.pageManager.notify();
            } finally {
                this.notifyCount-=1;
            }
        }
    }
    protected css_:StringF;
    css(c:StringF):ZUI{
        this.css_=c;
        return this;
    }
    protected applyCSS(jq:JQuery){
        if (this.css_){
            let css = ZUI.stringVal(this.css_);
            jq.attr("style",css);
        }
    }
    protected field_:string;
    field(f:string):ZUI{
        this.field_=f;
        return this;
    }
    protected style_:StringF;
    style(s:StringF):ZUI{
        this.style_=s;
        return this;
    }

    protected hideShow_:BooleanF;
    hideShow(hs:BooleanF):ZUI{
        this.hideShow_=hs;
        return this;
    }
    hide():boolean{
        if (this.hideShow_ instanceof Function){
            return this.hideShow_();
        } else {
            return this.hideShow_;
        }
    }

    static stringVal(content:StringF):string {
        let val = "";
        if (content instanceof Function){
            val = content();
        } else {
            val=content;
        }
        if (!val)
            val="";
        return val;
    }
    static numberVal(content:NumberF):number{
        let val = 0;
        if (content instanceof Function){
            val = content();
        } else {
            val=content;
        }
        return val;
    }
    
    classStr():string{
        let str = "";
        if (this.style_){
            let classes = ZUI.stringVal(this.style_);
            str =  classes+" ";
        } else
            str =  "";
        if (this.hideShow_){
            let hs = this.hide();
            if (hs)
                str+=" hidden ";
        }
        return str;
    }
    
    uniqueId():string {
        let id:string = "i"+Math.floor(Math.random()*1000000);
        return id;
    }
    waiting():JQuery{
        return $("<div>Waiting</div>");         // replace this with a spinner icon
    }
    error(msg:string):JQuery{
        return $(`ZUI ERROR ${msg}`)
    }
    static setState(fieldName:string,newVal:any){
        let pm = ZUI.pageManager;
        if (pm){
            let cp = pm.curPage;
            if (cp){
                let ps = cp.pageState;
                if (ps) ps[fieldName]=newVal;
            }
        }
    }
    static getState(fieldName:string):any{
        let pm = ZUI.pageManager;
        if (pm){
            let cp = pm.curPage;
            if (cp){
                let ps = cp.pageState;
                if (ps)  return ps[fieldName];
            }
        }
        return null;
    }
    static getUserKey():string{
        return ZUI.pageManager.getUserManager().getUserKey();
    }
}