

abstract class DataTableUIAbs extends ZUI{
    protected maxColumns:number;
    protected maxRows:number;
    protected firstRow:number;
    protected lastRow:number;
    protected firstCol:number;
    protected lastCol:number;
    protected nCols:number;
    protected nRows:number;
    protected cellEnter:(row:number,col:number)=>void;
    protected rowInc:number;
    protected colInc:number;
    constructor(maxRows=20,maxColumns=10,cellEnter?:(row:number,col:number)=>void){
        super();
        this.maxRows=maxRows;
        this.maxColumns=maxColumns;
        this.cellEnter=cellEnter;
    }
    setMaxes(maxRows:number,maxColumns:number){
        this.maxRows=maxRows;
        this.maxColumns=maxColumns;
        ZUI.notify();
    }
    setFirstCol(firstCol:number){
        this.firstCol=firstCol;
        ZUI.notify();
    }
    setFirstRow(firstRow:number){
        this.firstRow=firstRow;
        ZUI.notify();
    }
    protected tableStyle():string{ return "DataTableUI-table "+this.classStr()};
    abstract hasData():boolean;
    protected abstract colHeaderList():string[];
    protected abstract rowLabelList():string[];
    protected abstract rowData(row:number,firstCol:number,lastCol:number):any[];
    renderJQ():JQuery{
        let table = $(`<table class="${this.tableStyle()}"></table>`)
        let colHeaderList = this.colHeaderList();
        let rowLabelList = this.rowLabelList();
        if (this.hasData()){
            this.resolveRowsCols(rowLabelList.length,colHeaderList.length);
            let colHeaders = this.columnHeaderRow(colHeaderList);
            table.append(colHeaders);
            if (this.firstRow>0)
                table.append(this.upRow())
            for (let row=this.firstRow;row<=this.lastRow;row++){
                let rowData = this.rowData(row,this.firstCol,this.lastCol);
                let tableRow = this.tableRow(row,rowLabelList[row],rowData);
                table.append(tableRow);
            }
            if (this.lastRow<this.nRows-1)
                table.append(this.downRow())
        }
        this.applyCSS(table);
        return table;
    }
    protected columnHeaderRow(colHeaderList:string[]):JQuery{
        let rowJQ = $(`<tr><th></th></tr>`)
        if (this.firstCol>0)
            rowJQ.append(this.leftBtn());
        for (let col=this.firstCol;col<=this.lastCol;col++){
            let header = colHeaderList[col];
            let colJQ = $(`<th>${header}</th>`);
            rowJQ.append(colJQ);
        }
        if (this.lastCol<this.nCols-1)
            rowJQ.append(this.rightBtn());
        return rowJQ;
    }
    protected tableRow(rowIdx:number,rowLabel:string,rowData:any[]):JQuery{
        let row = $(`<tr><th>${rowLabel}</th></tr>`)
        if (this.firstCol>0)
            row.append(this.ellipsis());
        for (let colIdx=0;colIdx<rowData.length;colIdx++){
            let d = rowData[colIdx];
            let ds = "";
            if (d)
                ds=d.toString();
            else if (d==0)
                ds="0";
            let td=$(`<td>${ds}</td>`)
            td.mouseenter(()=>{
                if (this.cellEnter)
                    this.cellEnter(rowIdx,colIdx)
            })
            td.append(td);
            row.append(td);
        }
        if (this.lastCol<this.nCols-1)
            row.append(this.ellipsis())
        return row;
    }
    protected leftBtn():JQuery{
        let jq = $(`<th class="leftArrowBtn"></th>`)
        jq.click(()=>{
            this.firstCol-=this.colInc
            ZUI.notify();
        })
        return jq;
    }
    protected rightBtn():JQuery{
        let jq = $(`<th class="rightArrowBtn"></th>`)
        jq.click(()=>{
            this.firstCol+=this.colInc;
            ZUI.notify();
        })
        return jq;
    }
    protected upRow():JQuery{
        let row = $(`<tr></tr>`)
        row.append(this.upBtn())
        if (this.firstCol>0)
            row.append($(`<td></td>`))
        for (let col=this.firstCol;col<=this.lastCol;col++){
            row.append(this.ellipsis())
        }
        return row;
    }
    protected upBtn():JQuery{
        let jq = $(`<th class="upArrowBtn"></th>`)
        jq.click(()=>{
            this.firstRow-=this.rowInc;
            ZUI.notify();
        })
        return jq;
    }
    protected downRow():JQuery{
        let row = $(`<tr></tr>`)
        row.append(this.downBtn())
        if (this.firstCol>0)
            row.append($(`<td></td>`))
        for (let col=this.firstCol;col<=this.lastCol;col++){
            row.append(this.ellipsis())
        }
        return row;
    }
    protected downBtn():JQuery{
        let jq = $(`<th class="downArrowBtn"></th>`)
        jq.click(()=>{
            this.firstRow+=this.rowInc;
            ZUI.notify();
        })
        return jq;
    }
    protected ellipsis():JQuery{
        let jq = $(`<td class="DataTableUI-ell">...</td>`)
        return jq;
    }
    private resolveRowsCols(nRows:number, nCols:number){
        this.nRows=nRows;
        this.nCols=nCols;
        this.rowInc = Math.floor(this.maxRows*0.8);
        this.colInc = Math.floor(this.maxColumns*0.8);
        if (this.firstRow<0) this.firstRow=0;
        this.lastRow = this.firstRow+this.maxRows-1;
        if (this.lastRow>=nRows){
            this.lastRow=nRows-1;
            this.firstRow=this.lastRow-this.maxRows+1;
            if (this.firstRow<0) this.firstRow=0;
        }

        if (this.firstCol<0) this.firstCol=0;
        this.lastCol = this.firstCol+this.maxColumns-1;
        if (this.lastCol>=nCols){
            this.lastCol=nCols-1;
            this.firstCol = this.lastCol-this.maxColumns+1;
            if (this.firstCol<0) this.firstCol=0;
        }
    }
}