/// <reference path="ZUI.ts"/>
/// <reference path="PageManager.ts"/>

class DragDropWrapperUI extends ZUI {
    id:string;
    hoverStyle:string;
    hoverHighStyle:string;
    hoverLowStyle:string;
    payload:any; // drag payload or function to return drag payload
    doDrop:(payload:any,where: null | "high" | "low")=>void;
    constructor(content:ZUI){
        super();
        this.content=content;
        this.id=this.uniqueId();
        this.hoverStyle="DragDropWrapperUI-hover";
        this.hoverHighStyle="";
        this.hoverLowStyle="";
        PageManager.addAfterDOMNotice(()=>{
            let cnt = $("#"+this.id);
            cnt.on("dragenter",(e:JQueryEventObject)=>{
                this.dragEnter(e);
            })
            cnt.on("dragover",(e:JQueryEventObject)=>{
                this.dragOver(e);
            })
            cnt.on("dragleave",(e:JQueryEventObject)=>{
                this.dragLeave(e);
            })
            cnt.on("drop",(e:JQueryEventObject)=>{
                this.dropEvent(e);
            })
            cnt.on("dragstart",(e:JQueryEventObject)=>{
                this.dragStart(e);
            })
        })
    }
    protected dragEnter(e:JQueryEventObject){
        //DB.msg("dragEnter",e);
    }
    protected dragOver(e){
        //DB.msg("dragOver",e);
        if (this.id == $(e.currentTarget).attr("id")){
            e.preventDefault();
            let jq=$(e.currentTarget);
            let height = jq[0].offsetHeight;
            jq.removeClass(this.hoverHighStyle);
            jq.removeClass(this.hoverLowStyle);
            jq.removeClass(this.hoverStyle);
            if (this.doDrop){
                jq.addClass(this.hoverStyle);
                let y = e.offsetY;
                if (this.hoverLowStyle && y>height*0.5){
                    jq.addClass(this.hoverLowStyle);
                }
                if (this.hoverHighStyle && y<=height*0.5){
                    jq.addClass(this.hoverHighStyle);
                }
            }
        }
        
    }
    private eventPayload(event):any{
        let payloadST=event.originalEvent.dataTransfer.getData("zingpayload");
        if (payloadST){
            let payload = JSON.parse(payloadST);
            return payload;
        } else {
            return null;
        }
    }
    protected dragLeave(e:JQueryEventObject){
        //DB.msg("dragLeave",e);
        let jq=$(e.currentTarget);
        jq.removeClass(this.hoverHighStyle);
        jq.removeClass(this.hoverLowStyle);
        jq.removeClass(this.hoverStyle);
    }
    protected dropEvent(e){
        //DB.msg("dropEvent",e);
        let jq=$(e.currentTarget);
        jq.removeClass(this.hoverHighStyle);
        jq.removeClass(this.hoverLowStyle);
        jq.removeClass(this.hoverStyle);
        if (this.doDrop){
            let payload = this.eventPayload(e);
            if (payload){
                let height = jq[0].offsetHeight;
                let y=e.offsetY;
                let where = null;
                if (y<=height*0.5){
                    where = "high"
                }
                if (y>height*0.5){
                    where = "low"
                }
                //DB.msg(`y ${y} height ${height} where ${where}`)
                this.doDrop(payload,where);
            }
        }
    }
    protected dragStart(e){
        //DB.msg("dragStart",e);
        if (this.id == $(e.currentTarget).attr("id")){
            let payload = this.getPayload();
            e.originalEvent.dataTransfer.setData("zingpayload",payload)
        }
        
    }

    hover(hvStyle:string):DragDropWrapperUI{
        this.hoverStyle=hvStyle;
        return this;
    }
    high(hStyle:string):DragDropWrapperUI{
        this.hoverHighStyle=hStyle;
        return this;
    }
    low(hStyle:string):DragDropWrapperUI{
        this.hoverLowStyle=hStyle;
        return this;
    }
    private getPayload():string{
        let rslt=null;
        if (this.payload && typeof this.payload ==="function"){
            rslt = this.payload();
        } else {
            rslt = this.payload;
        }
        return JSON.stringify(rslt);
    }
    dragPayload(payload:any ):DragDropWrapperUI {  // the data to be transfered in the drag
                                    // or function to return that data
        this.payload=payload;
        return this;
    }

    drop(doDrop:(payload:any,where?: null | "high" | "low")=>void):DragDropWrapperUI {
        this.doDrop=doDrop;
        return this;
    }

    renderJQ():JQuery{
        let jq = super.renderJQ();
        jq.attr("id",this.id);
        this.applyCSS(jq);
        if (this.payload){
            jq.attr("draggable","true");
        }
        return jq;
    }
}