/// <reference path="DataSource.ts"/>

class MemDataSource extends DataSource {
    datastore:any;
    constructor(){
        super();
        this.datastore={};
    }

    GET(key:string, done:(err:string,data:DataObj)=>void,forceRequest:boolean,
            serverContext:ServerContext){
        let _this=this;
        
        let collectionKey = DataSource.collectionFromKey(key);
        let id = DataSource.idFromKey(key);
        let collection = this.datastore[collectionKey];
        if (!collection){
            this.datastore[collectionKey]={}
            collection = this.datastore[collectionKey];
        }
        let rec = collection[id];
        if (rec) {
            rec = DataObj.make(collectionKey,rec);
            rec.dataSource=_this;
            done(null,rec);
        } else
            done("key not found "+key,null);
        
    }
    cGET(key:string,forceRequest?:boolean):DataObj{
        let collectionKey = DataSource.collectionFromKey(key);
        let id = DataSource.idFromKey(key);
        let collection = this.datastore[collectionKey];
        if (!collection){
            this.datastore[collectionKey]={}
            collection = this.datastore[collectionKey];
        }
        let rec = collection[id];
        if (rec) {
            rec = DataObj.make(collectionKey,rec);
            rec.dataSource=this;
            return rec;
        } else{
            return null;
        }
    }
    GETm(keys:string[], done:(err:string,data:DataObj[])=>void,forceRequest:boolean,
            serverContext:ServerContext){
        this.multiGet(0,keys,[],done,forceRequest);
    }
    private multiGet(idx:number,keys:string[],rslt:DataObj[],done:(err:string,data:DataObj[])=>void,forceRequest:boolean){
        if (idx>=keys.length) {
            done(null,rslt);
        } else {
            this.GET(keys[idx],(err:string,data:DataObj)=>{
                if (err) {
                    done(err,rslt);
                } else {
                    rslt.push(data);
                    this.multiGet(idx+1,keys,rslt,done,forceRequest);
                }
            },forceRequest,null);
        }
    }
    GETBlob(blobKey:string,serverContext:ServerContext,done:(err:string,blob:BlobStoreRec)=>void){

        let collection = this.datastore["BLOB"];
        if (collection){
            let file:File = collection[blobKey];
            DB.msg("downloadFile memdatasource not done",file);
        }
    }
    cGETm(keys:string[],forceRequest?:boolean):DataObj[]{
        let rslt:DataObj[] = [];
        for (let i in keys){
            let key = keys[i];
            let obj = this.cGET(key,forceRequest);
            if (obj)
                rslt.push(obj);
        }
        return rslt;
    }
    PUT(obj:DataObj,done:(err:string,newObj:DataObj)=>void, serverContext:ServerContext){
        
        let _this=this;

        let key = obj._key;
        if (!key){
            let tc = obj.getTypeCode();
            key = DataSource.generateKey(tc);
            obj._key=key;
        }
        let collectionKey = obj.getTypeCode();
        let collection = this.datastore[collectionKey];
        if (!collection){
            this.datastore[collectionKey]={}
            collection = this.datastore[collectionKey];
        }
        let id = DataSource.idFromKey(key);
        collection[id]=obj.toJSON();
        obj.dataSource = _this;
        done(null,obj);
    }
    PUTm(objs:DataObj[],done:(err:string,newObjs:DataObj[])=>void,
            serverContext:ServerContext){
        this.multiPut(0,objs,[],done,serverContext);
    }    
    
    private multiPut(idx:number,objs:DataObj[],rslt:DataObj[],done:(err:string,newObjs:DataObj[])=>void,
            serverContext:ServerContext){
        if (idx>=objs.length){
            done(null,rslt);
        } else {
            this.PUT(objs[idx],(err:string,data:DataObj)=>{
                if (err){
                    done(err,rslt);
                } else {
                    rslt.push(data);
                    this.multiPut(idx+1,objs,rslt,done,serverContext);
                }
            },serverContext)
        }
    }
    PUTFileBlob(file:File,serverContext:ServerContext,done:(err:string,blobKey:string)=>void){
        let key = DataSource.generateKey("BLOB");
        let collection = this.datastore["BLOB"];
        if (!collection){
            this.datastore["BLOB"]={};
            collection = this.datastore["BLOB"];
        }
        let id = DataSource.idFromKey(key);
        collection[id]={ _id:id, _key:key, file:file};
        done(null,key);
    }
    PUTBlob(blob:Blob,name:string,serverContext:ServerContext,done:(err:string,blobKey:string)=>void){
        let key = DataSource.generateKey("BLOB");
        let collection = this.datastore["BLOB"];
        if (!collection){
            this.datastore["BLOB"]={};
            collection = this.datastore["BLOB"];
        }
        let id = DataSource.idFromKey(key);
        collection[id]={ _id:id, _key:key, blob:blob};
        done(null,key);
    }
    DELETE(key:string,done?:(err:string)=>void){
        super.DELETE(key,(err:string)=>{
            if (err) {
                if(done) done(err);
            } else {
                let collectionKey = DataSource.collectionFromKey(key);
                let id = DataSource.idFromKey(key);
                let collection = this.datastore[collectionKey];
                if (collection) {
                    let rec=collection[id];
                    collection[id]=null;
                    if (!rec){
                        if (done) done("no such object "+key);
                    } else {
                        if (done) done(null);
                    }
                } else {
                    if (done) done("no such object "+key);
                }
            
            }
        })
    }
    FIND(typeCode:string, search:Query,done:(err:string,keys:string[],objs:DataObj[])=>void,
            keysOnly:boolean,forceRequest:boolean,serverContext:ServerContext){

        let colCode = DataObj.typeToCollection(typeCode);
        let collection = this.datastore[colCode];
        let rslt:string[]=[];
        if (collection){
            for (let id in collection){
                let rec = collection[id];
                let match=true;
                for (let field in search){
                    let srch = search[field];
                    let recVal = rec[field];
                    if (!this.matches(srch,recVal))
                        match=false;
                }
                if (match)
                    rslt.push(rec._t_+"_"+id);
            }
            if (keysOnly){
                done(null,rslt,[]);
            } else {
                let dObjs:DataObj[] = [];
                this.getFromKeys(rslt,0,dObjs,(objs:DataObj[])=>{
                    done(null,rslt,objs);
                },serverContext);
            }
        } else {
            done(null,rslt,[]);
        }

    }
    matches(srch:QueryValue, val:any):boolean{
        return Query.matchesQV(srch,val)
    }
    cFIND(typeCode:string,search:Query,forceRequest?:boolean):string[]{

        let colCode = DataObj.typeToCollection(typeCode);
        let collection = this.datastore[colCode];
        let rslt:string[]=[];
        if (collection){
            for (let id in collection){
                let rec = collection[id];
                let match=true;
                for (let field in search){
                    let val = search[field];
                    let recVal = rec[field];
                    if (!recVal || recVal!=val)
                        match=false;
                }
                if (match)
                    rslt.push(rec._t_+"_"+id);
            }
            return rslt;
        } else {
            return null;
        }

    }
}