/// <reference path="./DataObj.ts"/>


class HTTPDataSource extends DataSource {
    urlRoot:string;
    constructor(urlRoot?:string){
        super();
        this.urlRoot=urlRoot;
    }
    isRemote():boolean { return true; }
    GET(key:string, done:(err:string,data:DataObj)=>void,forceRequest:boolean,
            serverContext:ServerContext){
        let _this=this;
        if (!key){
            done(null,null);
            return;
        }
        let url = `${this.urlRoot}data/get/${key}`;
        let typeCode=DataSource.collectionFromKey(key);
        DB.msg("HTTP GET ",url);
        let http = new XMLHttpRequest();
        http.open("GET",url);
        http.onreadystatechange=function() {
            if (this.readyState==4){
                if (this.status == 200){
                    let json = JSON.parse(http.responseText);
                    let obj = DataObj.make(typeCode,json)
                    obj.dataSource=_this;
                    done(null,obj);
                    _this.notify();
                } else
                    done(""+this.status+" "+this.statusText,null);
            }
        }
        http.send();
    }
    httpLogin(userName:string, password:string,done:(err:string,user:DataObj)=>void){
        DB.msg("http login",userName);
        let http = new XMLHttpRequest();
        let url = `${this.urlRoot}login?userName="${userName}"&password="${password}"`;
        http.open("GET",url);
        http.onreadystatechange=function(){
            if (this.readyState==4){
                if (this.status == 200){
                    let userJSONStr = this.responseText;
                    let userJSON = JSON.parse(userJSONStr);
                    let user = DataObj.make(null,userJSON);
                    done(null,user);
                } else {
                    done(`${this.status} ${this.responseText}`,null);
                }
            }
        }
        http.send();

    }
    httpLogout(done:(err:string)=>void){
        DB.msg("http logout");
        let http = new XMLHttpRequest();
        let url = `${this.urlRoot}logout`;
        http.open("GET",url);
        http.onreadystatechange=function(){
            if (this.readyState==4){
                if (this.status == 200){
                    done(null);
                } else {
                    done(`${this.status} ${this.responseText}`);
                }
            }
        }
        http.send();
    }
    getCurrentUserKey(done:(userKey:string)=>void,request?,response?){
        DB.msg("http getCurrentUserKey");
        let http = new XMLHttpRequest();
        let url = `${this.urlRoot}currentuser`;
        http.open("GET",url);
        http.onreadystatechange=function(){
            if (this.readyState==4){
                if (this.status == 200){
                    let userKey = this.responseText;
                    if (userKey.length==0){
                        done(null);
                    } else {
                        done(userKey);
                    }
                } else {
                    done(null);
                }
            }
        }
        http.send();
    }
    GETm(keys:string[],done:(err:string,data:DataObj[])=>void,forceRequest:boolean,
            serverContext:ServerContext){
        let _this=this;
        if (!keys){
            done(null,[]);
            return;
        }
        let url=`${this.urlRoot}data/getm`;
        let http=new XMLHttpRequest();
        http.open("POST",url);
        http.setRequestHeader("Content-Type","application/json");
        let jstr = JSON.stringify({ keys:keys });
        http.send(jstr);
        http.onreadystatechange=function() {
            if (this.readyState==4){
                if (this.status == 200){
                    let json = JSON.parse(http.responseText);
                    let rslt:DataObj[] = [];
                    for (let i in json){
                        let objDesc = json[i];
                        let mobj:DataObj = DataObj.make(null,objDesc);
                        mobj.dataSource=_this;
                        rslt.push(mobj);
                    }
                    if (done) done(null,rslt);
                    _this.notify();
                } else
                    if (done) done(""+this.status+" "+this.statusText,null);
            }
        }
    }
    GETBlob(blobKey:string,serverContext:ServerContext,done:(err:string,blob:BlobStoreRec)=>void){
        if (blobKey){
            let url = HTTPDataSource.urlFromBlobKey(blobKey);
            window.location.href=url;
        }
    }
    static urlFromBlobKey(blobKey:string){
        let url = `/blob/${blobKey}`;
        return url;
    }
    PUT(obj:DataObj,done:(err:string,newObj:DataObj)=>void,serverContext:ServerContext){
        let _this=this;
        let typeCode = obj.getTypeCode();
        let url = `${this.urlRoot}data/put/${obj._key}`;
        DB.msg("HTTP PUT ",url);
        let http = new XMLHttpRequest();
        http.open("POST",url);
        http.setRequestHeader("Content-Type","application/json");
        let sjson = obj.toJSON();
        let jstr = JSON.stringify(sjson);
        http.onreadystatechange=function() {
            if (this.readyState==4){
                if (this.status == 200){
                    let json = JSON.parse(http.responseText);
                    let mobj = DataObj.make(typeCode,json);
                    mobj.dataSource=_this;
                    if (done) done(null,mobj);
                    _this.notify();
                } else {
                    DB.msg(`PUT failure ${this.status} ${this.statusText}`,obj);
                    if (done) done(""+this.status+" "+this.statusText,null);
                }
            }
        }
        http.send(jstr);
    }

    createUser(userDesc:any,password:string,done:(err:string)=>void){
        let url = `${this.urlRoot}createuser?password="${password}"`;
        let http = new XMLHttpRequest();
        http.open("POST",url);
        http.setRequestHeader("Content-Type","application/json");
        let jstr = JSON.stringify(userDesc);
        http.onreadystatechange=function(){
            if (this.readyState==4){
                if (this.status==200){
                    done(null);
                } else {
                    done(`${this.status} ${this.responseText}`);
                }
            }
        }
        http.send(jstr);
    }
    PUTm(objs:DataObj[],done:(err:string,newObjs:DataObj[])=>void,serverContext:ServerContext){
        let _this=this;

        let url = `${this.urlRoot}data/putm`;
        let http = new XMLHttpRequest();
        let toSend=[];
        for (let i in objs){
            toSend.push(objs[i].toJSON());
        }
        http.open("POST",url);
        http.setRequestHeader("Content-Type","application/json");
        let jstr = JSON.stringify(toSend);
        http.send(jstr);
        http.onreadystatechange=function() {
            if (this.readyState==4){
                if (this.status == 200){
                    let json = JSON.parse(http.responseText);
                    let rslt:DataObj[] = [];
                    for (let i in json){
                        let objDesc = json[i];
                        let mobj = DataObj.make(null,objDesc);
                        mobj.dataSource=_this;
                        rslt.push(mobj);
                    }
                    if (done) done(null,rslt);
                    _this.notify();
                } else
                    if (done) done(""+this.status+" "+this.statusText,null);
            }
        }
    }
    PUTFileBlob(file:File,serverContext:ServerContext,done:(err:string,blobKey:string)=>void){
        let _this = this;
        let url=`${this.urlRoot}blob`;
        let formData = new FormData();
        formData.append('name',file.name)
        formData.append('file',file);

        let http = new XMLHttpRequest();
        http.open("POST",url,true);
        http.send(formData);
        http.onreadystatechange=function() {
            if (this.readyState==4){
                if (this.status == 200){
                    let json = JSON.parse(http.responseText);
                    if (done) done(null,json.blobKey);
                } else
                    if (done) done(""+this.status+" "+this.statusText,null);
                    _this.notify();
            }
        }
    }
    
    PUTBlob(blob:Blob,name:string,serverContext:ServerContext,done:(err:string,blobKey:string)=>void){
        let _this = this;
        let url=`${this.urlRoot}blob`;
        let formData = new FormData();
        DB.msg("HTTPDataSource.PUTBlob not done")
        formData.append('name',name)
        formData.append('data',blob);
        //formData.append('file',file);

        let http = new XMLHttpRequest();
        http.open("POST",url,true);
        http.send(formData);
        http.onreadystatechange=function() {
            if (this.readyState==4){
                if (this.status == 200){
                    let json = JSON.parse(http.responseText);
                    if (done) done(null,json.blobKey);
                } else
                    if (done) done(""+this.status+" "+this.statusText,null);
                    _this.notify();
            }
        }
    }
    DELETE(key:string,done?:(err:string)=>void){
        let _this = this;
        let url = `${this.urlRoot}data/delete/${key}`;
        DB.msg("HTTP DELETE ",url);
        let http = new XMLHttpRequest();
        http.open("DELETE",url);
        http.send();
        http.onreadystatechange=function() {
            if (this.readyState==4){
                if (this.status == 200){
                    if (done) done(null);
                    _this.notify();
                } else
                    if (done) done(""+this.status+" "+this.statusText);
            }
        }
    }
    FIND(typeCode:string, search:Query,done:(err:string,keys:string[],objs:DataObj[])=>void,
            keysOnly:boolean,forceRequest:boolean,serverContext:ServerContext){
        let _this=this;
        let colCode = DataObj.typeToCollection(typeCode);
        let url = `${this.urlRoot}data/find/${colCode}?`;
        let start=true;
        let query=this.buildSearchQuery(search);
        
        if (keysOnly){
            query+="&keysonly=1"
        }
        url+=query;
        DB.msg("HTTP FIND ",url);
        let http = new XMLHttpRequest();
        http.open("GET",url);
        http.send();
        http.onreadystatechange=function() {
            if (this.readyState==4){
                if (this.status == 200){
                    let json = JSON.parse(http.responseText);
                    let keys = json.keys;
                    let objs = json.objs;
                    let dObjs:DataObj[] = []
                    if (objs){
                        for (let o in objs){
                            let objJson = objs[o];
                            let obj = DataObj.make(null,objJson);
                            dObjs.push(obj);
                        }
                    }
                    if (done) done(null,keys,dObjs);
                    _this.notify();
                } else {
                    if (done) done(""+this.status+" "+this.responseText,null,null);
                }
            }
        }
    }
    private buildSearchQuery(search:Query):string{
        return "q="+JSON.stringify(search.toJSON());

    }

}