/// <reference path="DataTableAbs.ts"/>
/// <reference path="DataRow.ts"/>
/// <reference path="DataColumn.ts"/>

type DataTableJSON = {[colName:string]:string | number }[]
class DataTable extends DataTableAbs {
    protected contents:any[][] // [row,column]
    columnHeaderIdxs:{[columnHeader:string]:number}
    protected orderedColumnHeaders:string[]
    constructor(contents?:any[][],headers?:string[]){
        super();
        if (contents && headers){
            if (contents.length>0 && contents[0].length != headers.length){
                DB.msg("DataTable const wrong number of headers for data");
                return;
            }
            this.contents=contents;
            this.columnHeaderIdxs={};
            for (let i=0;i<headers.length;i++){
                this.columnHeaderIdxs[headers[i]]=i;
            }
        } else {
            this.contents=[];
            this.columnHeaderIdxs={};
        }
        this.rebuildOrderedColumnHeaders();
    }
    fromCSVStr(str:string,noHeaders?:boolean):DataTable{
        if (noHeaders){
            this.csvNoHeadFromStr(str);
        } else {
            this.csvHeadFromStr(str);
        }
        return this;
    }
    
    hasCol(colName:string):boolean{
        return colName in this.columnHeaderIdxs;
    }
    colName(colIdx:number):string{
        if (!this.orderedColumnHeaders)
            this.rebuildOrderedColumnHeaders()
        return this.orderedColumnHeaders[colIdx]
    }
    colIdx(colName:string):number{
        let idx =this.columnHeaderIdxs[colName]
        if (idx || idx==0)
            return idx;
        else
            return -1;
    }
    columnIndex():{[colname:string]:number}{
        return this.columnHeaderIdxs;
    }
    nRows():number{
        return this.contents.length;
    }
    columnHeaders():string[]{
        if (!this.orderedColumnHeaders){
            this.rebuildOrderedColumnHeaders();
        }
        return this.orderedColumnHeaders;
    }
    nCols():number{
        return this.orderedColumnHeaders.length;
    }
    static fromJSON(json:DataTableJSON):DataTable{
        let rslt = new DataTable();
        let nr = json.length;
        for (let r=0;r<nr;r++){
            let obj = json[r];
            for (let c in obj){
                let val = obj[c];
                rslt.set(r,c,val)
            }
        }
        return rslt
    }
    private csvHeadFromStr(data:string){
        this.contents=[];
        let rows:string[] = data.split("\n");
        let headerRow:string = this.trimReturnChars(rows[0]);
        let columnNames:string[] = this.splitRow(headerRow);
        this.buildColumnHeaders(columnNames);
        for (let r=1;r<rows.length;r++){
            let row =this.trimReturnChars(rows[r])
            if (row.length>0){
                let cells=this.splitRow(row);
                this.contents.push(cells);
            }
        }
        this.rebuildOrderedColumnHeaders();
        //DB.msg("contents",this.contents);
    }
    
    private csvNoHeadFromStr(data:string){
        this.contents=[];
        let rows:string[] = data.split("\n");
        for (let r=0;r<rows.length;r++){
            let row =this.trimReturnChars(rows[r])
            if (row.length>0){
                let cells=this.splitRow(row);
                for (let c in cells){
                    let colName="H"+c;
                    this.set(r,colName,cells[c])
                }
            }
        }
        this.rebuildOrderedColumnHeaders();
        //DB.msg("contents",this.contents);
    }
    private trimReturnChars(row:string){
        if (row.length>0 && row.charAt(row.length-1)=="\r")
            return row.substring(0,row.length-1);
        else
            return row;
    }
    private splitRow(row:string):string[]{
        if (row.charCodeAt(0)>300){
            row=row.slice(1);
        }
        let rslt:string[]=[];
        let charIdx=0;
        let endIdx=0;
        let sub="";
        while (charIdx<row.length){
            //DB.msg(row.substring(charIdx,50))
            switch (row.charAt(charIdx)){
                case '"':
                    endIdx = row.indexOf('"',charIdx+1);
                    if (endIdx<0){
                        sub = row.substring(charIdx+1,row.length-1);
                        charIdx=row.length;
                    } else {
                        sub = row.substring(charIdx+1,endIdx);
                        charIdx=endIdx+2;
                    }
                    rslt.push(sub);
                    break;
                case "'":
                    endIdx = row.indexOf("'",charIdx+1);
                    if (endIdx<0){
                        sub = row.substring(charIdx+1,row.length-1);
                        charIdx=row.length;
                    } else {
                        sub = row.substring(charIdx+1,endIdx);
                        charIdx=endIdx+2;
                    }
                    rslt.push(sub);
                    break;
                default:
                    if (row.charAt(charIdx)==","){
                        sub = undefined;
                        charIdx++;
                    } else {
                        endIdx = row.indexOf(',',charIdx+1);
                        if (endIdx<0){
                            sub = row.substring(charIdx,row.length);
                            charIdx=row.length;
                        } else {
                            sub = row.substring(charIdx,endIdx);
                            charIdx=endIdx+1;
                        }
                    }
                    rslt.push(sub);
                    break;
            }
        }
        return rslt;
    }
    private buildColumnHeaders(headers:string[]){
        this.columnHeaderIdxs={};
        for (let i=0;i<headers.length;i++){
            this.columnHeaderIdxs[headers[i]]=i;
        }
    }
    getI(row:number,col:number):any{
        if (row<0 || row>=this.contents.length)
            return 0;
        let contRow = this.contents[row];
        if (col<0 || col>=contRow.length)
            return 0;
        return this.contents[row][col]
    }
    protected setI(row:number,col:number,newVal:any){
        if (row<0) return;
        if (row>=this.contents.length){
            if ((row-this.contents.length>20)){
                let oldLength=this.contents.length;
                this.contents.length=row+1;
                for (let i=oldLength;i<this.contents.length;i++){
                    this.contents[i]=[];
                }
            } else {
                while (row>this.contents.length-1)
                    this.contents.push([]);
            }
        }
        
        let contRow = this.contents[row];
        if (col>=contRow.length){
            if ((col-contRow.length)>20){
                let oldLength=contRow.length;
                contRow.length=col+1;
                for (let i=oldLength;i<contRow.length;i++)
                    contRow[i]=0;
            } else {
                while (col>contRow.length-1){
                    contRow.push(0)
                }
            }
        }
        this.contents[row][col]=newVal;
    }
    get(row:number,col:string):string | number{
        let colIdx = this.columnHeaderIdxs[col];
        if (!colIdx && colIdx!=0){
            return 0;
        }
        return this.getI(row,colIdx)
    }
    set(row:number,col:string,newVal:string | number){
        let colIdx = this.columnHeaderIdxs[col];
        if (isNaN(colIdx)){
            colIdx = this.nCols();
            this.columnHeaderIdxs[col]=colIdx;
            if (!this.orderedColumnHeaders)
                this.rebuildOrderedColumnHeaders()
            else
                this.orderedColumnHeaders.push(col);
        }
        this.setI(row,colIdx,newVal);
    }
    addTo(row:number,col:string,newVal:number){
        let oldVal = this.get(row,col);
        if (isNaN(<number>oldVal)){
            this.set(row,col,newVal);
        } else {
            this.set(row,col,<number>oldVal+newVal);
        }
    }
    
    renameColumns(newNames:{[oldName:string]:string}){
        for (let oldName in newNames){
            let newName = newNames[oldName];
            if (oldName in this.columnHeaderIdxs){
                let idx = this.columnHeaderIdxs[oldName]
                delete this.columnHeaderIdxs[oldName]
                this.columnHeaderIdxs[newName]=idx;
            } else {
                DB.msg("DataTable.renameColumns no such column named "+oldName)
            }
        }
        this.rebuildOrderedColumnHeaders();
    }
    sortRows(colName:string,descending=false){
        if (!(colName in this.columnHeaderIdxs)){
            DB.msg("DataTable.sortRows no such column "+colName)
            return;
        }
        let colIdx:number = this.columnHeaderIdxs[colName]
        this.contents.sort((a,b)=>{
            let aVal = a[colIdx]
            let bVal = b[colIdx]
            switch(typeof aVal){
                case "number":
                    let vr=aVal-bVal;
                    if (descending) vr=-vr;
                    return vr;
                case "string":
                    let r = aVal.localeCompare(bVal);
                    if (descending) r=-r;
                    return r;
                default:
                    return 0;
            }
        })
    }
    protected rebuildOrderedColumnHeaders(){
        let len = Object.keys(this.columnHeaderIdxs).length;
        this.orderedColumnHeaders=new Array(len)
        for (let col in this.columnHeaderIdxs){
            let idx=this.columnHeaderIdxs[col];
            this.orderedColumnHeaders[idx]=col;
        }
    }

    replaceNulls(fractionOfColumnMin=0){
        if (fractionOfColumnMin==0){
            let nc = this.nCols();
            for (let c=0;c<nc;c++){
                for (let r=0;r<this.contents.length;r++){
                    let v = this.getI(r,c);
                    if (!v){
                        this.setI(r,c,0);
                    }
                }
            }
        } else {
            let nc = this.nCols();
            for (let c=0;c<nc;c++){
                let min = 10000000;
                for (let r=0;r<this.contents.length;r++){
                    let v = this.getI(r,c);
                    if (v && v<min){
                        min=v;
                    }
                }
                min = min*fractionOfColumnMin;
                for (let r=0;r<this.contents.length;r++){
                    let v = this.getI(r,c);
                    if (!v){
                        this.setI(r,c,min);
                    }
                }
            }
        }
    }
    parseNumbers(){
        let nr=this.nRows();
        for (let row=0;row<nr;row++){
            for (let col in this.columnHeaderIdxs){
                let v=this.get(row,col);
                if (typeof v == "string"){
                    let nv = parseFloat(v);
                    if (!isNaN(nv)){
                        this.set(row,col,nv);
                    }
                }
            }
        }
    }
    averageNormalizeByRow():DataTable{
        let rslt = new DataTable([],this.columnHeaders());
        let nr=this.nRows();
        for (let row=0;row<nr;row++){
            let sum=0;
            let count=0;
            let nc = this.columnHeaders().length;
            for (let col=0;col<nc;col++){
                let v = this.getI(row,col);
                if (typeof v=="number"){
                    sum+=v;
                    count++;
                }
            }
            if (count>0){
                let avg = sum/count;
                for (let col in this.columnHeaderIdxs){
                    let v = this.get(row,col);
                    if (typeof v=="number"){
                        v=v/avg;
                        rslt.set(row,col,v);
                    } else {
                        rslt.set(row,col,v);
                    }
                }
            }
        }
        return rslt;
    }
    scalerMult(scaler:number){
        for (let r in this.contents){
            let row=this.contents[r];
            for (let c in row){
                if (!isNaN(row[c])){
                    row[c]*=scaler;
                }   
            }
        }
    }
}
