/** 
 * These objects can be used to create queries that can be used in searches
 */

type QueryValue = number |string | boolean | Query
type NumberQuery = number | Query;
type StringQuery = string | Query;
type BooleanQuery = boolean | Query;
class Query{
    exp:any;
    private constructor(exp:any){
        this.exp=exp;
    }
    static OR(options:QueryValue[]):Query{
        let desc = { $$:"OR", options: options }
        return new Query(desc);
    }
    static AND(options:QueryValue[]):Query{
        return new Query ({ $$:"AND",options:options})
    }
    static range(from:QueryValue,to:QueryValue):Query{
        return new Query({ $$:"RANGE", from:from,to:to})
    }
    static anything():Query{
        return new Query({$$:"ANY"})
    }
    static dict(desc:{[attName:string]:QueryValue}):Query{
        let d:any ={$$:"DICT"};
        for (let k in desc){
            d[k]=desc[k];
        }
        return new Query(d)
    }
    static is(value:number|string|boolean):Query{
        return new Query( { $$:"IS",value:value})
    }
    static listContains(query:QueryValue):Query{
        return new Query( {$$:"LC",query:query})
    }
    static match(q1:QueryValue,q2:QueryValue):boolean{
        if (!q1)
            if (!q2) return true;
            else return false;
        else
            if (!q2) return false;
        if (typeof q1 != typeof q2) return false;
        switch(typeof q1){
            case "number":
            case "string":
            case "boolean":
                return q1==q2;
            default:{
                return this.matchQuery(<Query>q1,<Query>q2);
            }
        }
    }
    matches(val:any):boolean{
        if (val instanceof DataObj) return this.matchesDO(val);
        switch(this.exp.$$){
            case "OR":
                for (let i in this.exp.options){
                    let opt = this.exp.options[i];
                    if (Query.matchesQV(opt,val)) return true;
                }
                return false;
            case "AND":
                for (let i in this.exp.options){
                    let opt = this.exp.options[i];
                    if (!Query.matchesQV(opt,val)) return false
                }
                return true;
            case "RANGE":
                return this.rangeMatch(this.exp.from,this.exp.to,val);
            case "ANY":
                return true;
            case "DICT":
                return this.dictMatch(this.exp,val);
            case "IS":
                return this.exp.value == val;
            case "LC":
                for (let i in val){
                    let v = val[i];
                    if (Query.matchesQV(this.exp.query,v)) return true;
                }
                return false;

        }
    }
    static matchesQV(qv:QueryValue,val:any):boolean{
        switch(typeof qv){
            case "number":
            case "string":
            case "boolean":
                return qv == val;
            default:
                return qv.matches(val);
        }
    }
    attribute(attName:string):number | string | boolean | Query{
        if (this.exp.$$ != "DICT") return null;
        let att = this.exp[attName];
        return att;
    }

    toJSON():any{
        let src = this.exp;
        let rslt:any = { $$:src.$$ }
        switch(this.exp["$$"]){
            case "OR":
            case "AND":
                rslt.options = this.queryValueListToJSON(src.options)
                break;
            case "RANGE":
                rslt.from = this.queryValueToJSON(src.from);
                rslt.to = this.queryValueToJSON(src.to);
                break;
            case "ANY":
                break;
            case "DICT":
                rslt=this.dictToJSON(src);
                break;
            case "IS":
                rslt.value = this.queryValueToJSON(src.value);
                break;
            case "LC":
                rslt.value = this.queryValueToJSON(src.query);
                break;
        }
        return rslt;
    }
    
    static fromJSON(json:any):QueryValue{
        switch(typeof json){
            case "number":
            case "string":
            case "boolean":
                return json;
            default:
                switch(json.$$){
                    case "OR":
                        return Query.OR(Query.listFromJSON(json.options));
                    case "AND":
                        return Query.AND(Query.listFromJSON(json.options));
                    case "RANGE":
                        return Query.range(Query.fromJSON(json.from),Query.fromJSON(json.to))
                    case "ANY":
                        return Query.anything();
                    case "DICT":
                        let dict = {};
                        for (let i in json){
                            if (i!="$$"){
                                dict[i]=Query.fromJSON(json[i]);
                            }
                        }
                        return Query.dict(dict);
                    case "IS":
                        return Query.is(json.value)
                    case "LC":
                        return Query.listContains(Query.fromJSON(json.query))
                }
        }
    }
    private queryValueListToJSON(list:QueryValue[]):any{
        let rslt = [];
        for (let i in list){
            let item = list[i];
            rslt.push(this.queryValueToJSON(item))
        }
        return rslt;
    }
    private queryValueToJSON(val:QueryValue){
        switch(typeof val){
            case "number":
            case "string":
            case "boolean":
                return val;
            default:
                return val.toJSON();
        }
    }
    private dictToJSON(desc:{[attName:string]:QueryValue}):any{
        let rslt:any={ $$:"DICT"};
        for (let i in desc){
            rslt[i] = this.queryValueToJSON(desc[i])
        }
        return rslt;
    }

    private static listFromJSON(list:any[]):QueryValue[] {
        let rslt:QueryValue[]=[];
        for (let i in list){
            let item = Query.fromJSON(list[i]);
            rslt.push(item);
        }
        return rslt;
    }

    toMongo():any{
        let rslt:any={};
        
        switch(this.exp.$$){
            case "OR":
                let oList:[]=this.mongoOptionsList(this.exp.options);
                return { $or: oList }
            case "AND":
                return { $and: this.mongoOptionsList(this.exp.options)}
            case "RANGE":
                if (this.exp.from ){
                    rslt.$gte = this.exp.from;
                }
                if (this.exp.to){
                    rslt.$lte = this.exp.to;
                }
                return rslt;
            case "ANY":
                return { $exists: true};
            case "DICT":
                for (let i in this.exp){
                    if (i!="$$"){
                        let v = this.exp[i];
                        if (v) {
                            if (v.exp && v.exp.$$ && v.exp.$$=="DICT"){
                                Query.mongoCollapseDICT(rslt,i,v.exp);
                            } else
                                rslt[i]=Query.toMongoValue(v)
                        } else if (v==0)
                            rslt[i]=Query.toMongoValue(v)
                    }
                }
                return rslt;
            case "IS":
                return this.exp.value;
            case "LC":
                return { $elemMatch: Query.toMongoValue(this.exp.query)}
            default:
                return { badoperator:this.exp.$$}
        }
    }
    private static mongoCollapseDICT(rslt:{},key:string,v:{}){
        for (let k in v){
            if (k!="$$"){
                rslt[key+"."+k]=v[k];
            }
        }
    }
    private mongoOptionsList(options:QueryValue[]){
        let rslt:any=[];
        for (let i in options){
            let option = options[i];
            let mongOpt = Query.toMongoValue(option);
            rslt.push(mongOpt);
        }
        return rslt;
    }
    private static toMongoValue(val:QueryValue){
        switch(typeof val){
            case "number":
            case "string":
            case "boolean":
                return val;
            default:
                return val.toMongo();
        }
    }
    private static matchQuery(q1:Query,q2:Query):boolean{
        if (q1.exp.$$ != q2.exp.$$) return false;
        switch(q1.exp.$$){
            case "OR":
            case "AND":
                return this.matchOptionLists(q1.exp.options,q2.exp.options)
            case "RANGE":
                return this.match(q1.exp.from,q2.exp.from) && this.match(q1.exp.to,q2.exp.to);
            case "ANY":
                return true;
            case "DICT":
                return this.matchDicts(q1.exp,q2.exp);
            case "IS":
                return q1.exp.value == q2.exp.value;
            case "LC":
                return this.match(q1.exp.query,q2.exp.query);
            default:
                DB.msg("matchQuery got invalid $$",q1.exp.$$);
                return false;
        }
    }
    private static matchOptionLists(l1:QueryValue[],l2:QueryValue[]):boolean{
        if (l1.length!=l2.length) return false;
        for (let i in l1){
            let q=l1[i];
            if (!this.matchOptions(q,l2)) return false;
        }
        for (let i in l2){
            let q=l2[i];
            if (!this.matchOptions(q,l1)) return false;
        }
        return true;
    }
    private static matchOptions(q:QueryValue,list:QueryValue[]):boolean{
        for (let i in list){
            let lq = list[i];
            if (this.match(q,lq)) return true;
        }
        return false;
    }
    private static matchDicts(d1:{},d2:{}):boolean{
        for (let k in d1){
            let a1=d1[k];
            let a2=d2[k];
            if (!this.match(a1,a2)) return false;
        }
        for (let k in d2){
            let a1=d1[k];
            let a2=d2[k];
            if (!this.match(a1,a2)) return false;
        }
        return true;
    }
    
    private rangeMatch(from,to,val){
        let fromTrue = true;
        if (from){
            switch(typeof from){
                case "number":
                    fromTrue=from<=val;
                    break;
                case "string":
                    fromTrue = from.localeCompare(val)>=0;
                    break;
                default:
                    fromTrue=false;
                    break;
            }
        }
        let toTrue = true;
        if (to){
            switch(typeof to){
                case "number":
                    toTrue=val<=to;
                    break;
                case "string":
                    toTrue = val.localeCompare(to)>=0;
                    break;
                default:
                    toTrue=false;
                    break;
            }
        }
        return fromTrue && toTrue;
    }
    private dictMatch(dict:{[attName:string]:QueryValue},val):boolean{
        if (typeof val != "object") return false;
        for (let att in dict){
            if (att!="$$"){
                let dVal = dict[att];
                let vVal = val[att];
                if (!Query.matchesQV(dVal,vVal)) return false;
            }
        }
        return true;
    }
    
    
    matchesDO(obj:DataObj):boolean{
        switch(this.exp.$$){
            case "OR":
                for (let i in this.exp.options){
                    let opt = this.exp.options[i];
                    if (Query.matchesQV(opt,obj)) return true;
                }
                return false;
            case "AND":
                for (let i in this.exp.options){
                    let opt = this.exp.options[i];
                    if (!Query.matchesQV(opt,obj)) return false
                }
                return true;
            case "RANGE":
                return false;// no ranges on DataObj
            case "ANY":
                return true;
            case "DICT":
                return this.dictMatchDO(this.exp,obj);
            case "IS":
                return false; // no is test for DataObj
            case "LC":
                return false; // no list contains on DataObj
        }
    }
    private dictMatchDO(dict:{[attName:string]:QueryValue},obj:DataObj):boolean{
        for (let att in dict){
            if (att!="$$"){
                let dVal = dict[att];
                let oVal = obj["get"+att]();
                if (!Query.matchesQV(dVal,oVal)) return false;
            }
        }
        return true;
    }
}