/// <reference path="ZUI.ts"/>

class DoneIndicatorUI extends ZUI{
    getF_:Fnumber;
    barStyle_:string;
    clickF:()=>void;
    constructor(getF:Fnumber){
        super();
        this.getF_=getF;
        this.barStyle_="";
        this.clickF=null;
    }
    barStyle(barStyle_:string):DoneIndicatorUI{
        this.barStyle_=barStyle_;
        return this;
    }
    click(clickF:()=>void):DoneIndicatorUI{
        this.clickF=clickF;
        return this;
    }
    renderJQ():JQuery{
        let fraction = this.getF_();
        if (fraction<0)
            fraction=0;
        if (fraction>1)
            fraction=1;
        if (fraction>0 && fraction < 0.05)
            fraction=0.05;
        if (fraction <1 && fraction > 0.95)
            fraction = 0.95;
        let style="DoneIndicatorUI";
        if (this.clickF)
            style="DoneIndicatorUI-clickable"
        let jq=$(`<div class='${style} ${this.classStr()}'></div>`)
        let slider=$(`<div class='DoneIndicatorUI-slider ${this.barStyle_}'></div>`);
        slider.css("width",`${fraction*100}%`);
        jq.append(slider);
        jq.click(()=>{
            if (this.clickF){
                this.clickF();
                ZUI.notify();
            }
        })
        this.applyCSS(jq);
        return jq;
    }
}