/// <reference path="ZUI.ts"/>

class StyleCheckUI extends ZUI{
    checkVal: ()=>boolean;
    checkedStyle_:string;
    clickF:()=>void;
    constructor(checkVal:()=>boolean){
        super();
        this.checkVal=checkVal;
        this.checkedStyle_="StyleCheckUI-checked";
        this.clickF=null;
    }
    click(clickF:()=>void):StyleCheckUI{
        this.clickF=clickF;
        return this;
    }
    checkedStyle(checkedStyle:string):StyleCheckUI{
        this.checkedStyle_=checkedStyle;
        return this;
    }
    renderJQ():JQuery{
        let checked = this.checkVal();
        let cStyle="StyleCheckUI-checked "+this.checkedStyle_;
        if (!checked)
            if (this.classStr())
                cStyle="StyleCheckUI "+this.classStr();
            else
                cStyle="StyleCheckUI"

        let jq=$(`<div class='${cStyle}'></div>`);
        jq.click(()=>{
            if (this.clickF){
                this.clickF();
                ZUI.notify();
            }
        })
        this.applyCSS(jq);
        return jq;
    }
}