/// <reference path="DataTableUIAbs.ts"/>
/// <reference path="../analysis/DataTable.ts"/>

class DataTableUI extends DataTableUIAbs {
    protected dataTable:DataTableAbs;
    constructor(maxRows=20,maxCols=10,cellEnter?:(row:number,col:number)=>void){
        super(maxRows,maxCols,cellEnter);

    }
    setData(dataTable:DataTableAbs){
        this.dataTable=dataTable;
        this.firstRow=0;
        this.firstCol=0;
        //dataTable.db();
        ZUI.notify();
    }
    getData():DataTableAbs{
        return this.dataTable;
    }
    hasData(): boolean {
        if (this.dataTable) return true;
        return false;
    }
    protected colHeaderList(): string[] {
        if (this.dataTable){
            return this.dataTable.columnHeaders();
        } else {
            return null;
        }
    }
    protected rowLabelList(): string[] {
        if (this.dataTable){
            let rll:string[] = [];
            let nr=this.dataTable.nRows();
            for (let row=0;row<nr;row++){
                let rowLabel = (row+1).toFixed(0);
                rll.push(rowLabel);
            }
            return rll;
        }
        return null;
    }
    protected rowData(row: number, firstCol: number, lastCol: number): any[] {
        if (this.dataTable){
            let ch = this.dataTable.columnHeaders();
            let rowData:any[] = [];
            for (let col=firstCol;col<=lastCol;col++){
                let colName=ch[col];
                let v = this.dataTable.get(row,colName);

                rowData.push(v);
            }
            return rowData;
        } else {
            return null;
        }
    }
    
}