/// <reference path="UserManager.ts"/>
/// <reference path="ServerContext.ts"/>
/// <reference path="DataObj.ts"/>

abstract class RightsManager {
    dataSource: DataSource;
    userManager: UserManager;
    constructor(dataSource: DataSource, userManager: UserManager) {
        this.dataSource = dataSource;
        this.userManager = userManager;
        this.userManager.dataSource = this.dataSource;
    }
    /* Each of these methods verifies the ability for the current user
        to execute the corresponding method on the DataSource.
        If there are no problems, then null is returned.
        Otherwise a descriptive message string is returned showing the violation
    */

    abstract checkGET(key: string, serverContext: ServerContext, done: (granted: boolean) => void);
    checkGETm(keys: string[], serverContext: ServerContext, done: (granted: boolean) => void) {
        this.checkAllGET(keys, 0, serverContext, done);
    }
    abstract checkGETBlob(blobKey: string, serverContext, done: (granted: boolean) => void);
    abstract checkPUT(obj: DataObj, serverContext: ServerContext, done: (granted: boolean) => void);
    checkPUTm(objs: DataObj[], serverContext: ServerContext, done: (granted: boolean) => void) {
        this.checkAllPUT(objs, 0, serverContext, done);
    }
    abstract checkPUTBlob(serverContext: ServerContext, done: (granted: boolean) => void);
    abstract checkDELETE(key: string, serverContext: ServerContext, done: (granted: boolean) => void);
    abstract checkFIND(typeCode: string, search: Query, serverContext: ServerContext, done: (granted: boolean) => void);
    login(userId: string, password: string, done: (err: string, user: DataObj) => void, serverContext: ServerContext) {
        if (this.userManager)
            this.userManager.login(userId, password, done, serverContext);
        else
            done("RightsManager has no UserManager to login", null);
    }
    createUser(userDesc: any, password: string, done: (err: string) => void, serverContext: ServerContext) {
        if (this.userManager)
            this.userManager.createUser(userDesc, password, done, serverContext);
        else
            done("RightsManager has no UserManager to create user");
    }

    protected checkAllGET(keys: string[], idx: number, serverContext: ServerContext, done: (granted: boolean) => void) {
        if (idx >= keys.length) {
            done(true);
        } else {
            let obj = keys[idx];
            this.checkGET(obj, serverContext, (granted: boolean) => {
                if (granted) {
                    this.checkAllGET(keys, idx + 1, serverContext, done);
                } else {
                    done(false);
                }
            })
        }
    }
    protected checkAllPUT(objs: DataObj[], idx: number, serverContext: ServerContext, done: (granted: boolean) => void) {
        if (idx >= objs.length) {
            done(true);
        } else {
            let obj = objs[idx];
            this.checkPUT(obj, serverContext, (granted: boolean) => {
                if (granted) {
                    this.checkAllPUT(objs, idx + 1, serverContext, done);
                } else {
                    done(false);
                }
            })
        }
    }
}