/// <reference path="../data/DataSource.ts"/>
/// <reference path="ZUI.ts"/>

type PageState = { [stateVarName: string]: string };
type PageMap = { [pageName: string]: (state: PageState) => Page };
class PageManager implements DataMonitor {
    dataSource: DataSource;
    pageContentSelector: string;
    homePage: Page;
    private static pageMap: PageMap = {};
    private static curManager: PageManager;
    curPage: Page;
    private root: string;
    constructor(dataSource: DataSource, homePage: Page, pageContentSelector: string) {
        PageManager.curManager = this;
        this.dataSource = dataSource;
        dataSource.setDataMonitor(this);
        this.homePage = homePage;
        homePage.pageManager = this;
        this.curPage = null;
        this.pageContentSelector = pageContentSelector;
        let windowPath = window.location.pathname;
        window.onpopstate = (ev: PopStateEvent) => {
            this.doPopState(ev.state);
        }

        this.pageFromURLPath(windowPath);
        this.notify();
    }
    getPageState(): PageState {
        return this.curPage.pageState;
    }
    private static GETPageState(): PageState {
        return PageManager.curManager.curPage.pageState;
    }
    static GOTO(pageName: string, newState: PageState = PageManager.GETPageState()) {
        PageManager.curManager.goto(pageName, newState);
    }
    goto(pageName: string, newState: PageState = this.getPageState()) {
        if (PageManager.curManager && PageManager.curManager.curPage)
            newState.__pageName = pageName;

        let newURL = this.setupNextPage(pageName, newState);
        history.replaceState(newState, null, newURL);
        PageManager.curManager.notify();
    }
    static PUSHTO(pageName: string, newState: PageState = PageManager.GETPageState()) {
        PageManager.curManager.pushto(pageName, newState);
    }
    pushto(pageName: string, newState: PageState = this.getPageState()) {
        if (PageManager.curManager && PageManager.curManager.curPage)
            newState.__pageName = pageName;
        let newURL = this.setupNextPage(pageName, newState);
        history.pushState(newState, null, newURL);
        PageManager.curManager.notify();
    }
    private setupNextPage(pageName: string, newState: PageState) {
        let pageF: (state: PageState) => Page = PageManager.pageMap[pageName];
        if (pageF) {
            let newPage = pageF(newState);
            if (newPage) {
                PageManager.curManager.curPage = newPage;
                newPage.pageManager = this;
            } else {
                DB.msg(`page function on ${pageName} returned no page`, newState);
            }
        } else {
            DB.msg("no registerd page for " + pageName);
        }
        let root = PageManager.curManager.root;
        let queryStr = PageManager.queryFromState(newState);
        let newURL = root + "/pg/" + pageName + queryStr;
        return newURL;
    }
    private static queryFromState(newState: any) {
        if (typeof newState == "object") {
            let first = true;
            let queryStr = "";
            for (let paramName in newState) {
                if (paramName != "__pageName") {
                    let param = newState[paramName];
                    if (typeof param == "string") {
                        let paramStr = paramName + '=' + param;
                        paramStr = encodeURI(paramStr);
                        if (first) {
                            first = false;
                            queryStr = "?" + paramStr;
                        } else {
                            queryStr += "&" + paramStr;
                        }
                    }
                }
            }
            return queryStr;
        } else {
            return "";
        }
    }
    getUserManager(): UserManager {
        let ds = this.dataSource;
        if (ds) {
            let rm = ds.getRightsManager();
            if (rm) {
                let um = rm.userManager;
                return um;
            }
        }
        return null;
    }
    getUser(done: (user: DataObj) => void) {
        let um = this.getUserManager();
        if (um) {
            um.getUser(done);
        } else {
            done(null);
        }
    }
    getUserKey(): string {
        let um = this.getUserManager();
        let uk = um.getUserKey();
        return uk;
    }
    notify() {
        let pageContent = $(this.pageContentSelector);
        pageContent.empty();
        if (this.curPage) {
            let newContent = this.curPage.renderJQ();
            pageContent.empty();
            pageContent.append(newContent);
            PageManager.afterDOMNotify();
        } else {
            pageContent.html("******************** No Cur Page ***********************");

        }
    }
    static BACK() {
        PageManager.curManager.back();
    }
    back() {
      history.back();
      PageManager.curManager.notify();
    }

    static registerPageFactory(pageName: string, factory: (state: PageState) => Page) {
        PageManager.pageMap[pageName] = factory;
    }
    private doPopState(state: PageState) {
        if (!state) {
            let pn = this.homePage.pageName();
            this.setupNextPage(pn, { __pageName: pn });
            PageManager.curManager.notify();
            return;
        }
        if (state.__pageName) {
            this.setupNextPage(state.__pageName, state);
            PageManager.curManager.notify();
        }
    }
    private pageFromURLPath(windowPath: string) {

        let query = window.location.search;
        let newState = this.stateFromQuery(query);
        let parts = windowPath.split("/pg/");
        let pageName = parts[1] || "";
        this.root = parts[0];
        if (this.root.length <= 1)
            this.root = "/client";
        if (!this.getUserKey()) {
            this.curPage = this.homePage;
        } else {
            if (pageName.length == 0) {
                this.curPage = this.homePage;
                newState.__pageName = this.homePage.pageName();
            } else {
                let newPageF = PageManager.pageMap[pageName];
                if (newPageF) {
                    newState.__pageName = pageName;
                    this.curPage = newPageF(newState);
                } else {
                    DB.msg("no such page as " + pageName);
                }
            }
        }
        this.curPage.pageManager = this;
        this.notify();
    }
    private stateFromQuery(query: string): PageState {
        if (query.startsWith('?')) {
            query = query.substr(1, query.length-1);
        }
        let rslt: PageState = {};
        let decQuery = decodeURI(query);
        let qParts: string[] = decQuery.split("&");
        for (let i in qParts) {
            let qPart = qParts[i];
            let eqParts: string[] = qPart.split("=");
            let name = eqParts[0];
            let val = eqParts[1];
            if (name.length > 0)
                rslt[name] = val;
        }
        return rslt;
    }
    private static afterDOMList: any[];
    static addAfterDOMNotice(noticeF: () => void) {
        if (!PageManager.afterDOMList) {
            PageManager.afterDOMList = [];
        }
        PageManager.afterDOMList.push(noticeF);
    }
    private static afterDOMNotify() {
        for (let i in PageManager.afterDOMList) {
            let f = <() => void>PageManager.afterDOMList[i];
            f();
        }
    }
}

abstract class Page extends ZUI {
    pageState: PageState;
    pageManager: PageManager;
    constructor(pageState: PageState) {
        super();
        this.pageState = pageState;
        this.pageManager = null;

    }
    notify() {
        this.pageManager.notify();
    }
    getUserManager(): UserManager {
        let ds = DataObj.globalSource;
        if (ds) {
            let rm = ds.getRightsManager();
            if (rm) {
                return rm.userManager;
            }
        }
        return null;
    }
    getUserKey(): string {
        let um = this.getUserManager();
        if (um) {
            return um.getUserKey(null);
        }
        return null;
    }
    abstract pageName(): string;
}