/// <reference path="ZUI.ts"/>
/// <reference path="DivUI.ts"/>

class KeyListUI extends DivUI{
    protected keyListF:()=>KeyString[];
    protected itemViewF:(objKey:string)=>ZUI;
    protected keyMap:{ [objKey:string]:ZUI};
    protected sortF:(key1:string, key2:string)=>number;
    constructor(keyListF:()=>KeyString[]){
        super([]);
        //DB.msg("KeyListUI - const");
        this.keyListF=keyListF;
        this.keyMap={};
        this.style("KeyListUI");
    }
    itemView(itemViewF:(objKey:string)=>ZUI):KeyListUI{
        this.itemViewF=itemViewF;
        return this;
    }
    sort(sortF:(key1:string, key2:string)=>number):KeyListUI{
        this.sortF=sortF;
        return this;
    }
    protected buildKeyViewList(){
        //DB.start("KeyListUI.buildKeyViewList");
        let newKeyMap:{ [objKey:string]:ZUI} = {};
        let keyList = this.keyListF();
        if (keyList && this.sortF){
            keyList.sort(this.sortF);
        }
        this.items=[];
        for (let i in keyList){
            let key = keyList[i];
            let viewZUI = this.keyMap[key];
            if (!viewZUI){
                viewZUI = this.itemViewF(key);
            }
            newKeyMap[key]=viewZUI;
            this.items.push(viewZUI);
        }
        this.keyMap = newKeyMap;
        
        //DB.end("KeyListUI.buildKeyViewList");
    }
    renderJQ():JQuery{
        this.buildKeyViewList();
        return super.renderJQ();
    }

}