/// <reference path="ZUI.ts"/>
/// <reference path="../textprocessing/package.ts"/>

class TextEditUI extends ZUI {
    private editType:"text"|"html"|"code";
    private getF_:Fstring;
    private getBaseF_:Fstring;
    private setF_:(text:string)=>void;
    private jq:JQuery;
    private sourceText:string;
    private baseText:string;
    private beforeSel:{
        startLine:number,startOffset:number,endLine:number,endOffset:number
    }
    private afterSel:{
        startLine:number,startOffset:number,endLine:number,endOffset:number
    }
    private nLines:number;
    private keyCode:number;
    private majorEventInput:boolean;
    constructor(editType?:"text"|"html"|"code"){
        super();
        this.editType=editType;
        if (!editType){
            this.editType="text";
        }
    }
    setText(text:string){
        this.sourceText=text;
        if (this.jq){
            let html = this.renderTextIntoHTML(text,this.baseText);
            this.jq.html(html);
        }
    }
    getText():string{
        if (this.jq){
            this.sourceText=this.renderHTMLIntoText();
        }
        return this.sourceText;
    }
    setBase(text:string){
        this.baseText=text;
        if (this.jq){
            let html = this.renderTextIntoHTML(this.sourceText,this.baseText);
            this.jq.html(html);
        }
    }
    setF(eif:(text:string)=>void):TextEditUI{
        this.setF_=eif;
        return this;
    }
    private renderTextIntoHTML(text:string,baseText:string):string{
        let lines:string[] = [];
        if (text)
            lines = text.split("\n");
        let rslt="";
        let ld:LineDiff=null;
        if (baseText){
            ld = new LineDiff(text,baseText);
        }
        for (let i=0;i<lines.length;i++){
            let line = lines[i];
            let changeStyles="";
            if (ld){
                if (ld.fromLineReplaced(i))
                    changeStyles+=" TextEditUI-flr"
                if (ld.fromLineSkipped(i))
                    changeStyles+=" TextEditUI-fls"
                if (ld.toLineDeleted(i))
                    changeStyles+=" TextEditUI-tld"
            }
            let html=`<div class="TextEditUI-line ${changeStyles}" zel="${i}" contenteditable>${line}</div>`
            rslt+=html;
        }
        this.nLines=lines.length;
        return rslt;
    }

    private extractSelection():{
        startLine:number,startOffset:number,endLine:number,endOffset:number
    }{
        
        let sel=document.getSelection()
        let range=sel.getRangeAt(0)
        let startEl:Element = range.startContainer.parentElement;
        let startLine:number = parseInt(startEl.getAttribute("zel"));
        let startOffset:number=range.startOffset;
        let endEl = range.endContainer.parentElement;
        let endLine:number = parseInt(endEl.getAttribute("zel"));
        let endOffset:number = range.endOffset;
        return {startLine:startLine,startOffset:startOffset,endLine:endLine,endOffset:endOffset}
    }
    private renderHTMLIntoText():string{
        let rslt = '';
        let first=true;
        this.jq.find("div").each((index:number,elem:Element)=>{
            let html=elem.innerHTML;
            if (first){
                first=false;
                rslt=html;
            } else {
                rslt+="\n"+html;
            }
        });
        return rslt;
    }
    renderJQ():JQuery{
        this.getText();
        let val=ZUI.stringVal(this.getF_);
        if (!val)
            val="";
        this.jq=$(`<div class="TextEditUI-${this.editType} ${this.classStr()}" contenteditable>${val}</div>`);
        this.jq.on("input",(event:JQueryEventObject)=>{
            //DB.msg("cont.input",event.keyCode)
            //DB.msg("cont.text",this.getText())
            this.afterSel=this.extractSelection();
            this.handleMajorEventChange();
        })
        this.jq.on('keydown',(event:JQueryEventObject)=>{
            this.beforeSel=this.extractSelection();
            this.keyCode=event.keyCode;
            this.majorEventInput=false;
            if (event.ctrlKey) this.majorEventInput=true;
            //DB.msg("cont.keydown",event.keyCode)
            if (event.keyCode==9){
                event.preventDefault();
                this.handleTab();
            } else if (event.keyCode==13){
                event.preventDefault();
                this.handleReturn();
            } 
            //DB.msg("keydowncode",event.keyCode)
        });
        this.jq.on("blur",()=>{
            this.majorEventInput=true;
            this.handleMajorEventChange();
        })
        let html = this.renderTextIntoHTML(this.sourceText,this.baseText)
        this.jq.html(html);
        this.applyCSS(this.jq);
        return this.jq;
    }
    private handleMajorEventChange(){
        let majorEvent = false;
        let nChildren = this.jq.children.length;
        if (this.nLines!=nChildren)
            majorEvent=true;
        if (this.beforeSel.startLine!=this.beforeSel.endLine)
            majorEvent=true;
        if (this.afterSel.startLine!=this.afterSel.endLine)
            majorEvent=true;
        if (this.beforeSel.endLine!=this.afterSel.endLine)
            majorEvent=true;
        if (this.beforeSel.startLine!=this.afterSel.startLine)
            majorEvent=true;
        if (!this.keyCode)
            majorEvent=true;
        if (this.majorEventInput)
            majorEvent=true;
        if (majorEvent){
            if (this.setF_){
                let text = this.renderHTMLIntoText();
                this.setF_(text);
                this.setText(text);
                let zel=this.afterSel.endLine.toFixed();
                let children = this.jq[0].children;
                let selLineEl = children[this.afterSel.endLine]
                let selLineTxt = selLineEl.innerHTML;
                //let selLine = this.jq[0].children[this.afterSel.endLine][0]
                let range=new Range();
                //let selLineEl=selLine[0];
                let offset = this.afterSel.endOffset;
                if (selLineTxt.length<offset){
                    offset=offset-selLineTxt.length-1;
                    selLineEl=children[this.afterSel.endLine+1]
                }
                range.setStart(selLineEl.firstChild,offset);
                range.setEnd(selLineEl.firstChild,offset);
                let sel = document.getSelection();
                sel.removeAllRanges();
                sel.addRange(range);
            }
        }
    }
    private handleTab(){
        let sel=document.getSelection()
        let r=sel.getRangeAt(0)
        r.deleteContents();
        document.execCommand("insertHTML",false,"\t")
        r.collapse();
        //DB.msg("tabtext "+this.getText().length,this.getText())
    }
    
    private handleReturn(){
        let sel=document.getSelection()
        let r=sel.getRangeAt(0)
        let startLineText = r.startContainer.parentElement.innerHTML;
        let whiteSpace="";
        let i=0;
        while(startLineText.charAt(i)<=" " && i<startLineText.length){
            whiteSpace+=startLineText.charAt(i);
            i++;
        }
        r.deleteContents();
        document.execCommand("insertHTML",false,"\r"+whiteSpace)
        r.collapse();
        this.afterSel=this.extractSelection();
        this.majorEventInput=true;
        this.handleMajorEventChange()
        //DB.msg("rettext "+this.getText().length,this.getText())
    }
}