/// <reference path="../share/DB.ts"/>


abstract class DataTableAbs {
    abstract hasCol(ColName:string):boolean;
    abstract colName(colIdx:number):string;
    abstract colIdx(colName:string):number;
    abstract nRows():number;
    abstract columnHeaders():string[];
    abstract nCols():number;
    abstract get(row:number,col:string):string | number;
    abstract sortRows(colName:string,descending:boolean);
    toJSON():DataTableJSON{
        let rslt:DataTableJSON=[];
        let nr=this.nRows()
        let ch = this.columnHeaders();
        for (let r=0;r<nr;r++){
            let obj:{[colName:string]:number | string}={};
            for (let c in ch){
                let v = this.get(r,c);
                if (v || (<number>v)==0){
                    obj[c]=v;
                } 
            }
            rslt.push(obj);
        }
        return rslt;
    }
    
    getRow(row:number):DataRow{
        if (row<0 || row>=this.nRows())
            return null;
        let dr = new DataRow()
        for (let c of this.columnHeaders())
            dr.set(c,this.get(row,c))
        return dr;
    }
    getColumn(col:string):DataColumn{
        let rslt=new DataColumn(col);
        let nRows=this.nRows();
        for (let row=0;row<nRows;row++){
            rslt.set(row,this.get(row,col))
        }
        return rslt;
    }
    copy():DataTable{
        let rslt=new DataTable();
        let nRows=this.nRows();
        let ch = this.columnHeaders();
        for (let row=0;row<nRows;row++){
            for (let col of ch){
                let v = this.get(row,col);
                rslt.set(row,col,v);
            }
        }
        return rslt;
    }
    toCSVStr():string{
        let headStr = this.toCSVHead();
        let rslt = headStr;
        for (let r =0;r<this.nRows();r++){
            let rowStr = this.toCSVRow(r);
            rslt+="\n"+rowStr;
        }
        return rslt;
    }
    private toCSVHead():string{
        let ch = this.columnHeaders();
        let rslt = ch[0];
        for (let c=1;c<ch.length;c++){
            rslt+=","+ch[c]
        }
        return rslt;
    }
    private toCSVRow(r):string{
        let ch = this.columnHeaders();
        let rslt="";
        for (let col of ch){
            let val = this.get(r,col);
            if (rslt.length==0){
                rslt+=val;
            } else {
                rslt+=","+val;
            }
        }
        return rslt;
    }
    valueDensity():number {
        DB.start("valueDensity");
        let nFeatures = this.nCols() * this.nRows();
        let nFound=0;
        let ch = this.columnHeaders();
        for (let r=0;r<this.nRows();r++){
            for (let c in ch){
                let val = this.get(r,c);
                if (val!== undefined){
                    nFound++;
                }
            }
        }
        DB.end(`valueDensity ${nFound}/${nFeatures} ${nFound/nFeatures*100}%`)
        return nFound/nFeatures;
    }
    
    columnDensityCount(density:number){
        let ch = this.columnHeaders();
        let denseColCount=0;
        for (let col of ch){
            let cnt=0;
            for (let r=0;r<this.nRows();r++){
                let v = this.get(r,col)
                if (v)
                    cnt++
            }
            let colDense=cnt/this.nRows();
            if (colDense>density){
                denseColCount++
            }
        }
        DB.msg(`has ${denseColCount}/${this.nCols()} with feature density>${density}`)
    }
    
    sum():number{
        let total=0;
        let nRows=this.nRows();
        let ch = this.columnHeaders();
        for (let r =0;r<nRows;r++){
            for (let c of ch){
                let v= <number>this.get(r,c);
                if (!isNaN(v))
                    total+=v;
            }
        }
        return total;
    }
    count():number{
        let total=0;
        let nRows=this.nRows();
        let ch = this.columnHeaders();
        for (let r =0;r<nRows;r++){
            for (let c of ch){
                let v= this.get(r,c);
                if (v || v==0)
                    total++;
            }
        }
        return total;
    }
    countNumbers():number{
        let total=0;
        let nRows=this.nRows();
        let ch = this.columnHeaders();
        for (let r =0;r<nRows;r++){
            for (let c of ch){
                let v= this.get(r,c);
                if (!isNaN(<number>v))
                    total++;
            }
        }
        return total;
    }
    max():number{
        let foundNum=false;
        let max:number=0;;
        let nRows=this.nRows();
        let ch = this.columnHeaders();
        for (let r =0;r<nRows;r++){
            for (let c of ch){
                let v= <number>this.get(r,c);
                if (!isNaN(v)){
                    if (!foundNum){
                        max=v;
                        foundNum=true;
                    }
                    if (v>max){
                        max=v;
                    } 
                }   
            }
        }
        return max;
    }
    min():number{
        let foundNum=false;
        let min:number=0;
        let nRows=this.nRows();
        let ch = this.columnHeaders();
        for (let r =0;r<nRows;r++){
            for (let c of ch){
                let v= <number>this.get(r,c);
                if (!isNaN(v)){
                    if (!foundNum){
                        min=v;
                        foundNum=true;
                    }
                    if (v<min){
                        min=v;
                    } 
                }   
            }
        }
        return min;
    }
    
    db(msg?:string,maxColumns=5,maxRows=20){
        if (msg) DB.start(msg);
        DB.msg(`(${this.nRows()}x${this.nCols()})`)
        let rowEll=false;
        let colEll=false;
        let cols=this.nCols();
        if (cols>maxColumns){
            colEll=true;
            cols=maxColumns;
        }
        let rows=this.nRows();
        if (rows>maxRows){
            rowEll=true;
            rows=maxRows;
        }
        let headerStr = "\t";
        let ch = this.columnHeaders();
        for (let c=0;c<cols;c++){
            let col=ch[c];
            headerStr+=col+"\t";
        }
        if(colEll) headerStr+="..."
        DB.msg(headerStr);
        for (let r=0;r<rows;r++){
            let rowStr=`[${r}]:\t`;
            for (let c of ch){
                let val = this.get(r,c);
                if (typeof val == "string"){
                    rowStr+='"'+val+'"\t';
                } else {
                    rowStr+=val+"\t"
                }
            }
            if (colEll) rowStr+="...";
            DB.msg(rowStr);
        }
        if (rowEll) DB.msg("...")
        if (msg) DB.end(msg);
    }
}