/// <reference path="DataColumnAbs.ts"/>

class DataColumn extends DataColumnAbs{
    protected contents:any[]
    constructor(columnName:string,contents?:any[]){
        super(columnName);
        this.contents=[];
        if (contents){
            for (let i in contents){
                this.contents.push(contents[i]);
            }
        }
    }
    
    nValues():number{
        return this.contents.length;
    }
    static fromJSON(colName:string,json:(string | number)[]):DataColumn{
        let rslt = new DataColumn(colName);
        for (let r=0;r<json.length;r++){
            rslt.set(r,json[r]);
        }
        return rslt;
    }
    get(row:number):string | number{
        if (row<0 || row>=this.contents.length)
            return 0;
        return this.contents[row]
    }
    set(row:number,newVal:string | number){
        if (row<0) return;
        while (row>this.contents.length-1)
            this.contents.push(0);
        this.contents[row]=newVal;
    }
    addTo(row:number,newVal:number){
        let oldV = this.get(row);
        if (isNaN(<number>oldV)){
            this.set(row,newVal);
        } else {
            this.set(row,<number>oldV+newVal);
        }
    }
    
    scalerMult(scaler:number){
        for (let c in this.contents){
            if (!isNaN(this.contents[c])){
                this.contents[c]*=scaler;
            }   
        }
    }
}