/// <reference path="ZUI.ts"/>

class InputUI extends ZUI {
    getF_:Fstring;
    setF_:StringSetF;
    phF:StringF;
    fieldType:string;
    constructor(fieldType?:string){
        super();
        this.getF_=null;
        this.setF_=null;
        this.phF=null;
        this.fieldType=fieldType;
        if (!fieldType)
            this.fieldType="text";
    }
    getF(getFF:Fstring):InputUI{ 
        this.getF_=getFF;
        return this;
    }
    setF(setFF:StringSetF):InputUI{
        this.setF_=setFF;
        return this;
    }

    placeHolder(phF:StringF):InputUI{
        this.phF=phF;
        return this;
    }
    renderJQ():JQuery {
        let val = ZUI.stringVal(this.getF_);
        let ph = ZUI.stringVal(this.phF);
        let jq = $(`<input type='${this.fieldType}' class='InputUI ${this.classStr()}' value='${val}' placeholder='${ph}'/>`);
        jq.click((event)=>{
            event.stopImmediatePropagation()
        })
        jq.on("change",(event)=>{
            let newVal = jq.val();
            if (this.setF_){
                this.setF_(<string>newVal);
                jq.val(this.getF_());
            }
        })
        jq.on("paste",(event)=>{
            let oe:any = event.originalEvent;
            let content = oe.clipboardData.getData('text/plain');
            //DB.msg(`Paste "${content}`);
            this.setF_(content);
        })
        jq.on('drop',(event)=>{
            event.preventDefault();
            let oe:any=event.originalEvent;
            let data = oe.dataTransfer.getData("Text");
            //DB.msg(`Drop ${data}`)
            this.setF_(data);
        })
        this.applyCSS(jq);
        return jq;
    }   
}