

class ChartUI extends ZUI {
    private width:number;
    private height:number;
    private setWidth:number;
    private setHeight:number;
    private canvas:JQuery;
    private chartLeft:number;
    private chartRight:number;
    private chartTop:number;
    private chartBottom:number;
    private yAxisWidth:number;
    private xAxisHeight:number;
    protected xMax:number;
    protected yMax:number;
    protected xMin:number;
    protected yMin:number;

    private nYTicks:number;
    private nXTicks:number;

    private showYAxis:boolean;
    private showXAxis:boolean;

    private drawLineColor:string;
    private drawLineWidth:number;
    private graphics:CanvasRenderingContext2D;

    private dataType:string;
    private dataRow:DataRowAbs;
    private eventsSetup:boolean;
    private colHoverCallback:(col:string)=>void;
    private lastHoverIdx:number;
    private downX:number;

    private startColRange:number;
    private endColRange:number;
    private colRangeCallback:(startCol:number,endCol:number)=>void;

    private onClick:(colIdx:number)=>void;

    constructor(){
        super();
        this.setWidth=100;
        this.setHeight=50;
        this.yAxisWidth=50;
        this.xAxisHeight=20
        this.nYTicks=10;
        this.nXTicks=10;
        this.eventsSetup=false;
        this.showYAxis=true;
        this.drawLineColor="#FF0000";
        this.drawLineWidth=2;
        this.startColRange=-1;
        this.endColRange=-1;
        PageManager.addAfterDOMNotice(()=>{
            if (!this.eventsSetup && this.canvas){
                this.setupEventListeners();
                this.eventsSetup=true;
            }
        })
    }
    size(width:number,height:number):ChartUI{
        this.setWidth=width;
        this.setHeight=height;
        return this;
    }
    gridSize(nXTicks:number,nYTicks:number):ChartUI{
        this.nXTicks=nXTicks;
        this.nYTicks=nYTicks;
        return this;
    }
    setDataRow(dataRow:DataRowAbs){
        this.dataType="row";
        this.dataRow=dataRow;
        
        ZUI.notify();
    }
    colHover(colHoverCallback:(colName:string)=>void):ChartUI{
        this.colHoverCallback=colHoverCallback;
        return this;
    }
    colRangeSelect(colRangeCallback:(colStart:number,colEnd:number)=>void):ChartUI{
        this.colRangeCallback=colRangeCallback;
        return this;
    }
    click(onClick:(colIdx:number)=>void):ChartUI{
        this.onClick=onClick;
        return this;
    }
    xAxis(show=true):ChartUI{
        this.showXAxis=show;
        return this;
    }
    yAxis(show=true):ChartUI{
        this.showYAxis=show;
        return this;
    }
    line(lc:string,width=4):ChartUI{
        this.drawLineColor=lc;
        this.drawLineWidth=width;
        return this;
    }
    private toX(x:number):number{
        let newX= this.chartLeft+(x-this.xMin)/(this.xMax-this.xMin)
            *(this.chartRight-this.chartLeft);
        
        //DB.msg(`x${x} fromX${this.fromX(newX)}`)
        return newX;
    }
    private fromX(x:number):number{
        let newX = (x*2-this.chartLeft)/(this.chartRight-this.chartLeft)
            *(this.xMax-this.xMin)+this.xMin;
        return newX;
    } 
    private eventX(event:Event):number{
        let x=0;
        if (event instanceof MouseEvent){
            x = (<MouseEvent>event).clientX;
        } else if (event instanceof TouchEvent){
            x=(<TouchEvent>event).changedTouches[0].pageX
        } else {
            let oe = (<any>event).originalEvent
            if (oe){
                x = oe.clientX;
            } 
        }
        let rect = this.canvas[0].getBoundingClientRect();
        x = x-rect.left;
        return x;
    }
    private eventToChartX(event:Event):number {
        let x=this.eventX(event);
        x=this.fromX(x);
        return x;
    }
    private toY(y:number):number{
        let newY = this.chartBottom+(y-this.yMin)/(this.yMax-this.yMin)
            *(this.chartTop-this.chartBottom);
        //DB.msg(`y${y} fromY${this.fromY(newY)}`)
        return newY;
    }
    private fromY(y:number):number{
        let newY = (y*2-this.chartBottom)/(this.chartTop-this.chartBottom)
            * (this.yMax-this.yMin)+this.yMin;
        return newY;
    }
    private eventY(event:Event):number{
        let y=0;
        if (event instanceof MouseEvent){
            y = (<MouseEvent>event).clientY;
        } else if (event instanceof TouchEvent){
            y=(<TouchEvent>event).changedTouches[0].pageY
        } else {
            let oe = (<any>event).originalEvent
            if (oe){
                y = oe.clientY;
            } 
        }
        let rect = this.canvas[0].getBoundingClientRect();
        y = y-rect.top;
        return y;
    }
    private eventToChartY(event:Event):number {
        let y=this.eventY(event);
        y=this.fromY(y);
        return y;
    }
    private mouseMove(event:Event){
        let x = this.eventToChartX(event);
        let y = this.eventToChartY(event);
        if (this.colRangeCallback){
            if ((<any>event).originalEvent)
                event=(<any>event).originalEvent;
            if (event instanceof MouseEvent){
                let me = <MouseEvent>event;
                if (me.which==1){
                    this.expandRange(x);
                    this.paint();
                }
            }
        }
        if (this.colHoverCallback){
            if (x<this.xMin || x>this.xMax) return;
            let colIdx = Math.floor(x+0.5);
            if (colIdx!=this.lastHoverIdx){
                this.lastHoverIdx=colIdx;
                if (this.dataRow){
                    let colName = this.dataRow.columnHeaders()[colIdx]
                    this.colHoverCallback(colName);
                }
            }
        }
        //DB.msg(`mouseMove [${x},${y}]`)
    }
    expandRange(x:number){
        if (x<this.xMin)
            x=this.xMin;
        if (x>this.xMax)
            x=this.xMax;
        x=Math.floor(x+0.5)
        if (this.startColRange<0)
            this.startColRange=x;
        if (this.endColRange<0)
            this.endColRange=x;
        if (x<this.startColRange)
            this.startColRange=x;
        if (x>this.endColRange)
            this.endColRange=x;
    }
    private mouseDown(event:Event){
        this.downX=this.eventX(event);
        if (this.colRangeCallback){
            let x=this.eventToChartX(event);
            if (x<this.xMin)
                this.expandRange(this.xMin);
            else if (x>this.xMax)
                this.expandRange(this.xMax);
            else
                this.expandRange(x);
            this.paint();
        } else {
            this.startColRange=this.endColRange=-1;
        }
    }
    private mouseUp(event:Event){
        let upX = this.eventX(event);
        let dx=upX-this.downX;
        if (dx<0) dx=-dx;
        if (this.colRangeCallback){
            let x=this.eventToChartX(event);
            this.expandRange(x);
            if (this.startColRange>=0){
                let start=Math.floor(this.startColRange+0.5)
                let end=Math.floor(this.endColRange+0.5);
                if (dx>4)
                    this.colRangeCallback(start,end)
            }
            this.startColRange=this.endColRange=-1;
            this.paint();
        }
        if (this.onClick){
            let x = Math.floor(this.eventToChartX(event)+0.5);
            if (dx<=4)
                this.onClick(x);
        }
    }
    private mouseEnter(event:Event){
        if (this.colRangeCallback){
            if (event instanceof MouseEvent){
                if ((<MouseEvent>event).which==1){
                    let x = this.eventToChartX(event);
                    this.expandRange(x);
                    this.paint();
                }
            }
        }
    }
    private mouseLeave(event:Event){
        this.startColRange=this.endColRange=-1;
        this.paint();
    }
    private calculateChartArea(){
        this.chartLeft=this.yAxisWidth+2;
        this.chartRight=this.width-2;
        let m = this.graphics.measureText("A");
        let textPad = (m.actualBoundingBoxAscent+m.actualBoundingBoxDescent);
        this.chartTop=2+textPad;
        this.chartBottom=this.height-this.xAxisHeight-textPad;
    }
    private calculateAxesSpace(){
        if (this.dataType){
            switch(this.dataType){
                case "row":
                    this.computeRowAxes();
                    return;
                default:
                    break;
            }
        }
        this.yAxisWidth=0;
        this.xAxisHeight=0;
    }
    protected calculateDataBounds(){
        if (this.dataType){
            switch(this.dataType){
                case "row":
                    this.rowBounds();
                    return;
                default:
                    break;
            }
        }
        this.xMin=-10;
        this.yMin=-1;
        this.xMax=100;
        this.yMax=1;
    }

    private paint(){
        let g=this.graphics;
        g.font=ChartUI.labelFont;
        this.calculateDataBounds();
        this.calculateAxesSpace();
        this.calculateChartArea();
        g.strokeStyle="#000000";
        g.clearRect(0,0,this.width,this.height)
        this.paintSelection();
        this.paintGrid();
        this.paintAxes();
        g.strokeRect(this.chartLeft,this.chartTop,
            this.chartRight-this.chartLeft,this.chartBottom-this.chartTop);
        if (this.dataType){
            switch(this.dataType){
                case "row":
                    this.paintRow();
                    break;
                default:
                    break;
            }
        }
    }
    private paintGrid(){
        let g=this.graphics;
        g.font=ChartUI.labelFont;
        let xWidth = this.xMax-this.xMin
        let xStride = (xWidth)/this.nXTicks;
        let yWidth = this.yMax-this.yMin;
        let yStride = yWidth/this.nYTicks;
        g.strokeStyle="#888888";
        let saveDash = g.getLineDash();
        let saveWidth = g.lineWidth;
        g.lineWidth=3;
        g.setLineDash([6,9])
        g.beginPath();
        if (xStride>0){
            for (let x=xStride;x<xWidth;x+=xStride){
                g.moveTo(this.toX(x),this.toY(this.yMin));
                g.lineTo(this.toX(x),this.toY(this.yMax))
            }
        }
        if (yStride>0){
            for (let y=yStride;y<yWidth;y+=yStride){
                g.moveTo(this.toX(this.xMin),this.toY(y));
                g.lineTo(this.toX(this.xMax),this.toY(y))
            }
        }
        g.stroke();
        g.setLineDash(saveDash);
        g.lineWidth=saveWidth;
    }
    private paintAxes(){
        if (this.dataType){
            switch(this.dataType){
                case "row":
                    this.paintRowAxes();
                    return;
                default:
                    break;
            }
        }
        return;
    }
    private paintSelection(){
        if (this.startColRange<0) return;
        let start = this.toX(this.startColRange-0.5);
        let end = this.toX(this.endColRange+0.5);
        let g=this.graphics;
        let saveFill=g.fillStyle;
        g.fillStyle= "rgba(200,200,200,0.2)";
        g.fillRect(start,this.chartTop,end-start,this.chartBottom-this.chartTop);
        g.fillStyle=saveFill;
    }
    private paintRow(){
        let g=this.graphics;
        let saveStroke = g.strokeStyle;
        let saveWidth = g.lineWidth;
        g.strokeStyle=this.drawLineColor;
        g.lineWidth=this.drawLineWidth;
        g.beginPath();
        let nc=this.dataRow.nCols();
        let ch=this.dataRow.columnHeaders();
        let gap=true;
        for (let cIdx = 0; cIdx<nc;cIdx++){
            let v = <number>this.dataRow.get(ch[cIdx])

            if (isNaN(v)){
            } else {
                if (gap){
                    g.moveTo(this.toX(cIdx),this.toY(v))
                    gap=false;
                } else {
                    g.lineTo(this.toX(cIdx),this.toY(v));
                }
            }
        }
        g.stroke();
        g.strokeStyle=saveStroke;
        g.lineWidth=saveWidth;
    }
    private static yLabelOffset = 2;
    private static labelFont = "30px Arial"
    private paintRowAxes(){
        let g = this.graphics;
        let saveAlign = g.textAlign;
        let saveBase = g.textBaseline;
        g.fillStyle="#000000"
        if (this.showYAxis){
            g.textAlign="end"
            g.textBaseline="middle"
            let yWidth = this.yMax-this.yMin;
            let yStride = yWidth/this.nYTicks;
            if (yStride==0){
                let label=this.yMax.toString();
                g.fillText(label,this.chartLeft-ChartUI.yLabelOffset,this.chartTop);
                g.fillText(label,this.chartLeft-ChartUI.yLabelOffset,this.chartBottom);
            } else {
                let decimals=this.estimateDecimals(yStride);
                for (let y=this.yMin;y<=this.yMax;y+=yStride){
                    let label = y.toFixed(decimals);
                    if (decimals>10)
                        label=y.toString();
                    g.fillText(label,this.chartLeft-ChartUI.yLabelOffset,this.toY(y))
                }
            }
        }
        if (this.showXAxis){
            g.textAlign="center";
            g.textBaseline="bottom";
            let xWidth = this.xMax-this.xMin;
            let xStride = xWidth/this.nXTicks;
            if (xStride==0){
                let label = this.xMax.toString();
                g.fillText(label,this.chartLeft,this.height);
            } else {
                let ch = this.dataRow.columnHeaders();
                for (let x=this.xMin;x<this.xMax;x+=xStride){
                    let colIdx = Math.floor(x+0.5)
                    let label = ch[colIdx]//xLabel.toFixed(decimals);
                    g.fillText(label,this.toX(x),this.height);
                }
            }
        }
        g.textAlign=saveAlign;
        g.textBaseline=saveBase;
    }
    private rowBounds(){
        this.xMin=0;
        this.xMax=this.dataRow.nCols()-1;
        this.yMin=this.dataRow.min();
        this.yMax=this.dataRow.max();
    }
    private computeRowAxes(){
        this.computeRowYAxis();
        this.computeRowXAxis();
    }
    private computeRowYAxis(){
        if (this.showYAxis){
            let g=this.graphics;
            let yWidth = this.yMax-this.yMin;
            let yStride = yWidth/this.nYTicks;
            if (yStride==0){
                let label=this.yMax.toString();
                let width=g.measureText(label).width+ChartUI.yLabelOffset;
                this.yAxisWidth=width;
            } else {
                let decimals=this.estimateDecimals(yStride);
                this.yAxisWidth=0;
                for (let y=0;y<=yWidth;y+=yStride){
                    let label = y.toFixed(decimals);
                    if (decimals>10)
                        label=y.toString();
                    let width=g.measureText(label).width+ChartUI.yLabelOffset;
                    if (width>this.yAxisWidth)
                        this.yAxisWidth=width;
                }
            }
        } else {
            this.yAxisWidth=0;
        }
    }
    private computeRowXAxis(){
        if (this.showXAxis){
            let g = this.graphics;
            let m = g.measureText("A");
            this.xAxisHeight=m.actualBoundingBoxAscent+m.actualBoundingBoxDescent;
            
        } else {
            this.xAxisHeight=0;
        }
    }
    private estimateDecimals(range:number):number{
        let decimals = 0;
        let nr = range;
        while (nr<1){
            nr*=10;
            decimals ++
            if (decimals>10) return decimals;
        }
        return decimals;
    }

    setupEventListeners(){
        let canvas:JQuery = this.canvas;
        canvas.mousemove((event: Event) => {
            try {
                event.preventDefault();
                this.mouseMove(event);
            } catch (e) { DB.msg("mousemove ERR",e); }
        });
        canvas.mouseup((event:Event)=>{
            try {
                event.preventDefault();
                this.mouseUp(event)
            }catch (e) {DB.msg("mouseup Err",e)}
        })
        canvas.mousedown((event:Event)=>{
            try {
                event.preventDefault();
                this.mouseDown(event)
            }catch (e) {DB.msg("mousedown Err",e)}
        })
        
        canvas.mouseleave((event:Event)=>{
            try {
                event.preventDefault();
                this.mouseLeave(event)
            }catch (e) {DB.msg("mouseleave Err",e)}
        })
        canvas.mouseenter((event:Event)=>{
            try {
                event.preventDefault();
                this.mouseEnter(event)
            }catch (e) {DB.msg("mouseenter Err",e)}
        })
    }
    renderJQ():JQuery{
        this.eventsSetup=false;
        this.canvas= $(`<canvas class="ChartUI ${this.classStr()}" width=${this.setWidth}px height=${this.setHeight}px style="width:${this.setWidth}px;height:${this.setHeight}"></canvas>`);
        this.width=this.setWidth*2;
        (<any>this.canvas[0]).width=this.width;
        this.height=this.setHeight*2;
        (<any>this.canvas[0]).height=this.height;
        this.graphics = (<HTMLCanvasElement>this.canvas[0]).getContext("2d")
        this.paint();
        this.applyCSS(this.canvas);
        return this.canvas;
    }
}