
class ServerContext{
    request:any;
    response:any;
    isLogin:boolean;
    constructor(request,response){
        this.request=request;
        this.response=response;
        this.isLogin=false;
    }
    setUserKey(userKey:string){
        this.response.cookie("currentUserKey",userKey);
    }
    getUserKey():string {
        let key = this.request.cookies["currentUserKey"];
        return key;
    }
}