/// <reference path="ZUI.ts"/>

class ButtonUI extends ZUI{
    title:StringF;
    onclick:(event:Event)=>void;
    enabled:()=>boolean;
    constructor(title:StringF){
        super();
        this.title=title;
    }
    click(onclick:(event:Event)=>void):ButtonUI{
        this.onclick=onclick;
        return this;
    }
    enable(enabled:()=>boolean):ButtonUI {
        this.enabled=enabled;
        return this;
    }
    renderJQ():JQuery{
        let title = ZUI.stringVal(this.title);
        let jq = $(`<button class='ButtonUI ${this.classStr()}' ></button>`);
        if (this.enabled && !this.enabled()){
            jq.addClass("ButtonUI-disabled");
        }
        jq.html(title);
        if((!this.enabled || this.enabled()) && this.onclick){
            jq.click((event)=>{
                this.onclick(event);
            })
        }
        this.applyCSS(jq);
        return jq;
    }
}