/// <reference path="ZUI.ts"/>

class IconButtonUI extends ZUI{
    private url:string;
    private onclick:(event:Event)=>void;
    private iconName:string;
    private selected_:()=>boolean;
    private jq:JQuery;
    constructor(iconName:string,url:string,){
        super();
        this.iconName=iconName;
        this.url=url;
        this.onclick = onclick;
    }
    click(onclick:(event:Event)=>void):IconButtonUI{
        this.onclick=onclick;
        return this;
    }
    selected(selectedF:()=>boolean):IconButtonUI{
        this.selected_=selectedF;
        return this;
    }
    getIconName():string {
        return this.iconName;
    }
    renderJQ():JQuery{
        this.jq = $(`<button class="IconButtonUI ${this.classStr()}"></button>`);
        this.jq.html(`<img src="${this.url}"/>`);
        if (this.selected_ && this.selected_())
            this.jq.addClass("IconButtonUI-selected");
        else
            this.jq.removeClass("IconButtonUI-selected");
        this.jq.click((event:Event)=>{ this.onclick(event)})
        this.applyCSS(this.jq);
        return this.jq; 
    }
}