/// <reference path="ZUI.ts"/>


class DropDownChoiceUI extends ZUI {
    getF_:()=>string;
    setF_:(val:string)=>void;
    choices: {selectCode:string, label:string}[];
    constructor(){
        super();
        this.choices = [];
    }
    getF( f:()=>string):DropDownChoiceUI{
        this.getF_=f;
        return this;
    }
    setF( f:(val:string)=>void):DropDownChoiceUI{
        this.setF_=f;
        return this;
    }
    choice(selectCode:string,label:string):DropDownChoiceUI{
        this.choices.push({selectCode:selectCode,label:label});
        return this;
    }
    renderJQ():JQuery{
        let select = null;
        if (this.getF_){
            select = this.getF_();
        }
        let base = $(`<select class="DropDownChoiceUI ${this.classStr()}"></select>`);
        for (let i in this.choices){
            let choice = this.choices[i];
            let cJQ:JQuery = null;
            if (choice.selectCode==select){
                cJQ=$(`<option value="${choice.selectCode}" selected>${choice.label}</option>`)
            } else {
                cJQ=$(`<option value="${choice.selectCode}">${choice.label}</option>`)
            }
            base.append(cJQ);
        }
        base.change(()=>{
            if (this.setF_){
                this.setF_(base.val());
            }
        })
        this.applyCSS(base);
        return base;
    }
}