/// <reference path="ZUI.ts"/>

class DivUI extends ZUI{
    items: ZUIListF;
    constructor(items:ZUIListF){
        super();
        this.items = items;
    }
    renderJQ():JQuery{
        let items:ZUI[]=null;
        if (this.items instanceof Function)
            items = this.items();
        else
            items = this.items;
        let jq = $(`<div class='DivUI ${this.classStr()}'></div>`);

        for (let i in items){
            let curItem=items[i];
            let itemJQ = curItem.renderJQ();
            jq.append(itemJQ);
        }
        this.applyCSS(jq);
        return jq;
    }
}