/// <reference path="DivUI.ts"/>

class ClickWrapperUI extends DivUI{
    onclick:(event:Event)=>void;
    id:string;
    constructor(items:ZUIListF){
        super(items);
        this.id = this.uniqueId();
        PageManager.addAfterDOMNotice(()=>{
            if (this.onclick)
                $("#"+this.id).click(this.onclick);
        })
    }
    click(onclick:(event:Event)=>void):ClickWrapperUI{
        this.onclick=onclick;
        return this;
    }
    renderJQ():JQuery {
        let jq = super.renderJQ();
        jq.attr("id",this.id);
        this.applyCSS(jq);
        return jq;
    }
}