/// <reference path="ZUI.ts"/>

class TextUI extends ZUI{
    private textS:StringF;
    constructor(text:StringF){
        super();
        this.textS=text;
    }
    renderJQ():JQuery{
        let val = ZUI.stringVal(this.textS);
        let jq = $(`<div class='TextUI ${this.classStr()}'>${val}</div>`);
        this.applyCSS(jq);
        return jq;
    }
}