/// <reference path="ZUI.ts"/>

class BreakUI extends ZUI{
    private textS:StringF;
    constructor(text?:StringF){
        super();
        this.textS=text;
        if (!text)
            this.textS="";
    }
    renderJQ():JQuery{
        let val = ZUI.stringVal(this.textS);
        if (!val || val.length==0){
            val = "BreakUI";
        }
        let jq = $(`<div class='BreakUI ${this.classStr()}'>${val}</div>`);
        this.applyCSS(jq);
        return jq;
    }
}