
type DataTableRowLabeledJSON={[rowLabel:string]:{[colLabel:string]:(string | number)}}

abstract class DataTableRowLabeledAbs {
    abstract hasCol(colName:string):boolean;
    abstract hasRow(rowName:string):boolean;
    abstract nRows():number;
    abstract rowLabel(rowIdx:number):string;
    abstract rowIdx(rowLabel:string):number;
    abstract rowLabels():string[];
    abstract columnHeaders():string[];
    abstract nCols():number;
    abstract colName(colIdx:number):string;
    abstract colIdx(colName:string):number;
    abstract get(row:string,col:string):string|number;
    abstract renameColumns(newNames:{[oldName:string]:string})
    abstract relabelRows(newLabels:{[oldLabel:string]:string})

    copy():DataTableRowLabeled{
        let rslt = new DataTableRowLabeled();
        let rl=this.rowLabels();
        let ch = this.columnHeaders();
        for (let r in rl){
            for (let c in ch){
                let v = this.get(r,c);
                rslt.set(r,c,v);
            }
        }
        return rslt;
    }
    toJSON():DataTableRowLabeledJSON{
        let rslt:DataTableRowLabeledJSON={};
        let rl=this.rowLabels();
        let ch = this.columnHeaders();
        for (let r of rl){
            let rowObj:{[colName:string]:(string | number)} = {};
            for (let c of ch){
                let v = this.get(r,c);
                if (v || (<number>v)==0)
                    rowObj[c]=v;
            }
            rslt[r]=rowObj;
        }
        return rslt;
    }
    sum():number{
        let total=0;
        let rl = this.rowLabels();
        let ch = this.columnHeaders();
        for (let r of rl){
            for (let c of ch){
                let v = <number>this.get(r,c);
                if (!isNaN(v))
                    total+=v;
            }
        }
        return total;
    }
    count():number{
        let total=0;
        let rl = this.rowLabels();
        let ch = this.columnHeaders();
        for (let r of rl){
            for (let c of ch){
                let v = <number>this.get(r,c);
                if (v || 0==(v))
                    total++;
            }
        }
        return total;
    }
    countNumbers():number{
        let total=0;
        let rl = this.rowLabels();
        let ch = this.columnHeaders();
        for (let r of rl){
            for (let c of ch){
                let v = <number>this.get(r,c);
                if (!isNaN(v))
                    total++;
            }
        }
        return total;
    }
    max():number{
        let foundNum=false;
        let max:number=0;
        let rl = this.rowLabels();
        let ch = this.columnHeaders();
        for (let r of rl){
            for (let c of ch){
                let v = <number>this.get(r,c);
                if (!isNaN(v)){
                    if (!foundNum){
                        max=v;
                        foundNum=true;
                    }
                    if (v>max){
                        max=v;
                    } 
                }   
            }
        }
        return max;
    }
    min():number{
        let foundNum=false;
        let min:number=0;
        let rl = this.rowLabels();
        let ch = this.columnHeaders();
        for (let r of rl){
            for (let c of ch){
                let v = <number>this.get(r,c);
                if (!isNaN(v)){
                    if (!foundNum){
                        min=v;
                        foundNum=true;
                    }
                    if (v<min){
                        min=v;
                    } 
                }   
            }
        }
        return min;
    }
    
    db(msg?:string,maxColumns=5,maxRows=20){
        if (msg) DB.start(msg);
        DB.msg(`(${this.nRows()}x${this.nCols()})`)
        let rowEll=false;
        let colEll=false;
        let cols=this.nCols();
        if (cols>maxColumns){
            colEll=true;
            cols=maxColumns;
        }
        let rows=this.nRows();
        if (rows>maxRows){
            rowEll=true;
            rows=maxRows;
        }
        let headerStr = "\t";
        let ch = this.columnHeaders();
        let rl = this.rowLabels();
        for (let c=0;c<cols;c++){
            let col=ch[c];
            headerStr+=col+"\t";
        }
        if(colEll) headerStr+="..."
        DB.msg(headerStr);


        for (let r=0;r<rows;r++){
            let rowStr=`[${rl[r]}]:\t`;
            for (let c=0;c<cols;c++){
                let val = this.get(this.rowLabel(r),this.colName(c));
                if (typeof val == "string"){
                    rowStr+='"'+val+'"\t';
                } else {
                    rowStr+=val+"\t"
                }
            }
            if (colEll) rowStr+="...";
            DB.msg(rowStr);
        }
        if (rowEll) DB.msg("...")
        if (msg) DB.end(msg);
    }
}