/// <reference path="ZUI.ts"/>

class SliderUI extends ZUI {
    getF_:Fnumber;
    setF_:NumberSetF;
    minF_:NumberF;
    maxF_:NumberF;
    constructor(min:NumberF,max:NumberF){
        super();
        this.getF_=null;
        this.setF_=null;
        this.minF_=min;
        this.maxF_=max;
    }
    getF(getFF:Fnumber):SliderUI{ 
        this.getF_=getFF;
        return this;
    }
    setF(setFF:NumberSetF):SliderUI{
        this.setF_=setFF;
        return this;
    }
    renderJQ():JQuery {
        let val = ZUI.numberVal(this.getF_);
        let jq = $(`<input type='range' class='SliderUI ${this.classStr()}' value='${val}'`+
            ` min='${ZUI.numberVal(this.minF_)}' max='${ZUI.numberVal(this.maxF_)}'/>`);

        jq.on("change",(event)=>{
            let newVal = Number.parseFloat(<string>jq.val());
            if (this.setF_){
                this.setF_(newVal);
            }
        })
        this.applyCSS(jq);
        return jq;
    }
}