
type RowTableJSON = {[colName:string]:number | string}
abstract class DataRowAbs {
    
    abstract hasCol(colName:string):boolean;
    abstract colIdx(colName:string):number;
    abstract colName(colIndex:number):string;
    abstract columnHeaders():string[];
    abstract nCols():number;
    abstract get(col:string):string | number;
    abstract sort(descending?:boolean);
    abstract renameColumns(newNames:{[oldName:string]:string})
    toJSON():RowTableJSON{
        let rslt:RowTableJSON={};
        for (let c in this.columnHeaders()){
            let val = this.get(c);
            if (val || (<number>val)==0){
                rslt[c]=val;
            }
        }
        return rslt;
    }
    copy():DataRow{
        let newDR = new DataRow();
        for (let colName of this.columnHeaders()){
            newDR.set(colName,this.get(colName))
        }
        return newDR;
    }
    
    sum():number{
        let total=0;
        for (let col of this.columnHeaders()){
            let v = <number>this.get(col);
            if (!isNaN(v))
                total+=v;
        }
        return total;
    }
    sumSquares():number{
        let total=0;
        for (let col of this.columnHeaders()){
            let v = <number>this.get(col);
            if (!isNaN(v))
                total+=v*v;
        }
        return total;
    }

    pairProduct(row2:DataRowAbs):DataRow{
        let rslt=new DataRow();
        let nc = this.nCols();
        for (let col of this.columnHeaders()){
            let v1=<number>this.get(col);
            let v2=<number>row2.get(col);
            if (isNaN(v1) || isNaN(v2)){
                rslt.set(col,0);
            } else {
                rslt.set(col,v1*v2)
            }
        }
        return rslt;
    }
    dotProduct(row2:DataRowAbs):number {
        let rslt=0;
        let nc=this.nCols();
        for (let col of this.columnHeaders()){
            let v1=<number>this.get(col);
            let v2=<number>row2.get(col);
            if (!isNaN(v1) && !isNaN(v2)){
                rslt+=v1*v2
            }
        }
        return rslt;
    }
    count():number{
        let total=0;
        for (let c of this.columnHeaders()){
            let v=this.get(c);
            if (v || v==0)
                total++;
        }
        return total;
    }
    countNumbers():number{
        let total=0;
        for (let c of this.columnHeaders()){
            let v=this.get(c);
            if (!isNaN(<number>v))
                total++;
        }
        return total;
    }
    max():number{
        let ch=this.columnHeaders();
        let max=<number>this.get(ch[0]);
        for (let c of ch){
            let v = <number>this.get(c);
            if (!isNaN(v)){
                if (v>max){
                    max=v;
                } 
            }   
        }
        return max;
    }
    min():number{
        let ch=this.columnHeaders();
        let min=<number>this.get(ch[0]);
        for (let c of ch){
            let v = <number>this.get(c);
            if (!isNaN(v)){
                if (v<min){
                    min=v
                } 
            }   
        }
        return min;
    }
    
    subSet(startIdx:number,endIdx:number):DataRow{
        let rslt = new DataRow();
        if (startIdx>endIdx){
            let tmp=startIdx;
            startIdx=endIdx;
            endIdx=tmp;
        }
        let ch=this.columnHeaders();
        for (let i=startIdx;i<=endIdx;i++){
            let colName = ch[i];
            let v = this.get(colName);
            rslt.set(colName,v);
        }
        return rslt;
    }
    
    db(msg?:string,maxColumns=20){
        if (msg)
            DB.start(msg);
        let cols = this.nCols();
        let ellipses=false;
        if (cols>maxColumns){
            cols=maxColumns;
            ellipses=true;
        }
        let ch=this.columnHeaders();
        for (let c=0;c<cols;c++){
            let col = ch[c];
            let val = this.get(col);
            if (isNaN(<number>val))
                DB.msg(`[${col}]:"${val}"`)
            else
                DB.msg(`[${col}]:${val}`)
        }
        if (ellipses) DB.msg("...")
        if (msg)
            DB.end(msg)
    }
}