/// <reference path="ZUI.ts"/>
/// <reference path="ZUITypes.ts"/>

type StringSource = string | StringF | string[] ;
class ImageUI extends ZUI{
    private urlSource: StringSource;
    constructor(urlSource:StringSource){
        super();
        this.urlSource=urlSource;
    } 
    private url():string{
        let sc = this.urlSource;
        if (typeof sc == "function"){
            sc = sc();
        }
        if (sc instanceof Array){
            let r = Math.random();
            let l=sc.length;
            r = Math.floor(r*l)
            if (r>=l) r = l-1;
            sc = sc[r];
        }
        return sc;
    }
    renderJQ():JQuery {
        let url = this.url();
        let jq = $(`<img src="${url}" class="ImageUI ${this.classStr()}"/>`)
        this.applyCSS(jq);
        return jq;
    }
}