/*type DataSearchRangeSpec = { from: string | number, to: string | number};
type NumberQuery = number | {from?:number,to?:number};
type StringQuery = string | {from?:string, to?:string};
type FieldQuery = NumberQuery | StringQuery | boolean;
type DataSearchSpec = { [fieldName:string]:FieldQuery}*/
interface DataMonitor {
    notify();
}
class DataSource {
    private rightsManager_:RightsManager; // any will be replaced with actual interface later
    private dataMonitor:DataMonitor;
    constructor(){
        DataObj.globalSource=this;
    }
    setDataMonitor(dataMonitor:DataMonitor){
        this.dataMonitor=dataMonitor;
    }
    getRightsManager():RightsManager{
        return this.rightsManager_;
    }
    setRightsManager(rm:RightsManager){
        this.rightsManager_=rm;
    }
    isRemote():boolean { return false}
    notify(){
        if (this.dataMonitor) 
            this.dataMonitor.notify();
    }
    GET(key:string, done:(err:string,data:DataObj)=>void,forceRequest:boolean,
            serverContext:ServerContext){
        done(null,null);
    }
    canGet(key:string,serverContext:ServerContext,done:(granted:boolean)=>void){
        let rm = this.rightsManager_;
        if (rm){
            rm.checkGET(key,serverContext,done);
        } else {
            done(false);
        }
    } 
    cGET(key:string,forceRequest?:boolean):DataObj{
        DB.msg( "cGET This data source has no caching");
        return null;
    }
    GETm(keys:string[],done:(err:string,data:DataObj[])=>void,forceRequest:boolean,
            serverContext:ServerContext){
        done(null,null);
    }
    canGETm(keys:string[],serverContext:ServerContext,done:(granted:boolean)=>void){
        let rm=this.rightsManager_
        if (rm){
            rm.checkGETm(keys,serverContext,done);
        } else {
            done(false);
        }
    }
    cGETm(keys:string[],forceRequest?:boolean):DataObj[]{
        DB.msg( "cGETm This data source has no caching");
        return null;
    }
    
    PUT(obj:DataObj,done:(err:string,newObj:DataObj)=>void,serverContext:ServerContext){
        done(null,null);
    }
    canPUT(obj:DataObj,serverContext:ServerContext,done:(granted:boolean)=>void){
        let rm=this.rightsManager_;
        if (rm){
            rm.checkPUT(obj,serverContext,done);
        } else {
            done(false);
        }
    }
    canGETBlob(blobKey:string,serverContext:ServerContext,done:(granted:boolean)=>void){
        let rm=this.rightsManager_;
        if(rm){
            rm.checkGETBlob(blobKey,serverContext,done);
        } else {
            done(false);
        }
    }
    GETBlob(blobKey:string,serverContext:ServerContext,done:(err:string,blob:BlobStoreRec)=>void){
        DB.msg("data source has no downloadFile",this);
    }
    canPUTBlob(serverContext:ServerContext,done:(granted:boolean)=>void){
        let rm = this.rightsManager_;
        if (rm){
            rm.checkPUTBlob(serverContext,done);
        } else {
            done(false);
        }
    }
    PUTBlob(blob:Blob,name:string,serverContext:ServerContext,done:(err:string,blobKey:string)=>void){
        done(null,null);
    }
    PUTFileBlob(file:File,serverContext:ServerContext,done:(err:string,blobKey:string)=>void){
        done(null,null);
    }
    PUTm(objs:DataObj[],done:(err:string,newObjs:DataObj[])=>void,serverContext:ServerContext){
        done(null,null);
    }
    canPUTm(objs:DataObj[],serverContext:ServerContext,done:(granted:boolean)=>void){
        let rm=this.rightsManager_;
        if (rm){
            rm.checkPUTm(objs,serverContext,done);
        } else {
            done(false);
        }
    }
    DELETE(key:string,done?:(err:string)=>void){
        done(null)
    }
    canDELETE(key:string,serverContext:ServerContext,done:(granted:boolean)=>void){
        let rm=this.rightsManager_;
        if (rm){
            rm.checkDELETE(key,serverContext,done);
        } else {
            done(false);
        }
    }
    FIND(typeCode:string, search:Query,done:(err:string,keys:string[],objs:DataObj[])=>void,
            keysOnly:boolean,forceRequest:boolean,serverContext:ServerContext){
        done(null,null,null);
    }
    protected getFromKeys(keys:string[],idx:number,objs:DataObj[],done:(objs:DataObj[])=>void,serverContext:ServerContext){
        if (idx>=keys.length){
            done(objs);
        } else {
            this.GET(keys[idx],(err:string,data:DataObj)=>{
                if (!err){
                    objs.push(data);
                    this.getFromKeys(keys,idx+1,objs,done,serverContext);
                }
            },false,serverContext);
        }
    }
    canFIND(typeCode:string,search:Query,serverContext:ServerContext,done:(granted:boolean)=>void){
        let rm=this.rightsManager_;
        if (rm){
            rm.checkFIND(typeCode,search,serverContext,done);
        } else {
            done(false);
        }
    }
    cFIND(typeCode:string,search:Query,keysOnly:boolean,forceRequest:boolean):string[]{
        DB.msg( "cFIND no caching on this data service");
        return null;
    }
    static collectionFromKey(key:string):string{
        if (!key){
            DB.msg("no key for collection");
            return "";
        }
        let parts = key.split("_");
        let typeCode = parts[0];
        let colCode = DataObj.typeToCollection(typeCode);
        return colCode;
    }
    static typeFromKey(key:string):string{
        let parts = key.split("_");
        return parts[0];
    }
    static idFromKey(key:string):string {
        let parts = key.split("_");
        return parts[1];
    }
    static generateKey(typeCode:string):string {
        let id = Math.random()*1000000;
        id = Math.floor(id);
        return typeCode+"_"+id;
    }
    static filterKeyList(expectedCode:string, keyList:string[]):string[] {
        let rslt:string[] = [];
        for (let idx in keyList){
            let key = keyList[idx];
            let ty = DataSource.typeFromKey(key);
            if (DataObj.checkTypeCode(expectedCode,ty)){
                rslt.push(key);
            }
        }
        return rslt;
    }
    touched(obj:DataObj):boolean{
        this.notify();
        return true;
    }
    login(userId:string,password:string,done:(err:string)=>void,serverContext?:ServerContext){
        if (this.getRightsManager())
            this.getRightsManager().login(userId,password,done,serverContext);
        else
            done("DataSource has no RightsManager to login");
    }
    httpLogin(userId:string,password:string,done:(err:string,user:DataObj)=>void){
        done("No httpLogin for this data source",null);
    }
    httpLogout(done:(err:string)=>void){
        done("No httpLogin for this data source");
    }
    createUser(userDesc:any,password:string,done:(err:string)=>void,serverContext?:ServerContext){
        if (this.getRightsManager())
            this.getRightsManager().createUser(userDesc,password,done,serverContext);
        else
            done("DataSource has no RightsManager to create user");
    }

}
