/// <reference path="LineDiff.ts"/>

class StringDiff {
    private lFrom:string;
    private lTo:string;
    private heap:DiffEdit[];
    private addMemory:{[hash:number]:DiffEdit}
    private patch:DiffEdit[];
    private fromCharReplaced_:{[fromLine:number]:boolean}
    private fromCharSkipped_:{[fromLine:number]:boolean}
    private toCharDeleted_:{[fromLine:number]:boolean}
    constructor(from:string,to:string){
        this.lFrom=from;
        this.lTo=to
        this.patch = this.editsForS1IntoS2();
        this.indexPatch();
    }
    getPatch():DiffEdit[]{
        return this.patch;
    }
    fromCharReplaced(fromLine:number):boolean { return this.fromCharReplaced_[fromLine]}
    fromCharSkipped(fromLine:number):boolean { return this.fromCharSkipped_[fromLine]}
    toCharDeleted(fromLine:number):boolean { return this.toCharDeleted_[fromLine]}
    protected indexPatch(){
        this.fromCharReplaced_={};
        this.fromCharSkipped_={};
        this.toCharDeleted_={};
        if (this.patch){
            for (let i=0;i<this.patch.length;i++){
                let edit = this.patch[i];
                switch(edit.edit){
                    case "start":
                        break;
                    case "match":
                        break;
                    case "replace":
                        this.fromCharReplaced_[edit.fromIdx]=true;
                        break;
                    case "skipFrom":
                        this.fromCharSkipped_[edit.fromIdx]=true;
                        break;
                    case "addTo":
                        this.toCharDeleted_[edit.fromIdx]=true;
                        break;
                }
            }
        }
    }
    private editsForS1IntoS2():DiffEdit[]{
        this.heap=[ ]
        this.addMemory={}
        let top = new DiffEdit(0,0,"start",0)
        while (top.fromIdx<this.lFrom.length || top.toIdx<this.lTo.length){
            if (top.fromIdx<this.lFrom.length){
                let skip = new DiffEdit(top.fromIdx+1,top.toIdx,"skipFrom",top.totalCost+1,top);
                this.add(skip);
            }
            if (top.toIdx<this.lTo.length){
                let add = new DiffEdit(top.fromIdx,top.toIdx+1,"addTo",top.totalCost+1,top);
                this.add(add);
            }
            if (top.fromIdx<this.lFrom.length && top.toIdx<this.lTo.length){
                let mc = this.matchCost(top.fromIdx,top.toIdx);
                let edit:DiffEditCode="replace";
                if (mc==0)
                    edit="match"
                let match = new DiffEdit(top.fromIdx+1,top.toIdx+1,edit,
                    top.totalCost+2*mc,top);
                this.add(match);
            }
            top = this.get();
        }
        let rslt = this.reverseEdits(top);
        return rslt;
    }
    protected matchCost(fromIdx:number,toIdx:number):number{ 
            // returns 0 for no cost (match) and 1 for complete missmatch or in between
        let fromS = this.lFrom.charCodeAt(fromIdx);
        let toS = this.lTo.charCodeAt(toIdx);
        if (fromS==toS)
            return 0
        else
            return 0.99999
    }
    private reverseEdits(top:DiffEdit):DiffEdit[]{
        if (top && top.edit!="start"){
            let prev:DiffEdit[] = this.reverseEdits(top.prevState);
            top.fromIdx--;
            top.toIdx--;
            prev.push(top);
            return prev;
        } else {
            return [];
        }
    }
    private add(edit:DiffEdit){
        let hash = edit.hash();
        let mem = this.addMemory[hash];
        if (mem && mem.totalCost<=edit.totalCost) 
            return;
        this.addMemory[hash]=edit;
        let lastIdx = this.heap.length;
        this.heap.push(edit);
        while (lastIdx>0){
            let upIdx = Math.floor(lastIdx/2);
            if (this.heap[lastIdx].totalCost<this.heap[upIdx].totalCost){
                this.exchange(lastIdx,upIdx);
                lastIdx=upIdx;
            } else {
                lastIdx=-1;
            }
        }
    }
    private exchange(i1:number,i2:number){
        let tmp = this.heap[i1];
        this.heap[i1]=this.heap[i2]
        this.heap[i2]=tmp;
    }
    private get():DiffEdit{
        if (this.heap.length>0){
            let rslt = this.heap[0];
            this.heap[0]=this.heap.pop();
            let idx=0;
            while (idx<this.heap.length){
                let left = idx*2+1;
                if (left<this.heap.length&&this.heap[left].totalCost<this.heap[idx].totalCost){
                    this.exchange(left,idx);
                    idx=left;
                } else {
                    let right = left+1;
                    if (right<this.heap.length&&this.heap[right].totalCost<this.heap[idx].totalCost){
                        this.exchange(right,idx);
                        idx=right;
                    } else {
                        idx=this.heap.length;
                    }
                }
            }
            return rslt;
        } else {
            return null;
        }
    }
    applyEditFromTo(edits:DiffEdit[]):string{
        let rslt="";
        for (let i=0;i<edits.length;i++){
            let edit = edits[i];
            switch (edit.edit){
                case "start":
                    break;
                //case "addFrom":
                case "match":
                case "replace":
                    rslt+=this.lTo.charAt(edit.toIdx)
                    break;
                case "addTo":
                    rslt+=this.lTo.charAt(edit.toIdx)
                    break;
                case "skipFrom":
                //case "skipTo":
                    break;
            }
        }
        return rslt;
    }
}