/// <reference path="DataTableUI.ts"/>
/// <reference path="../analysis/DataTableRowLabeled.ts"/>

class DataTableRowLabeledUI extends DataTableUIAbs{
    protected dataTable:DataTableRowLabeled;
    private rowLabels:string[];
    constructor(maxRows=20,maxCols=10,cellEnter?:(row:number,col:number)=>void){
        super(maxRows,maxCols,cellEnter);

    }
    
    setData(dataTable:DataTableRowLabeled){
        this.dataTable=dataTable;
        this.firstRow=0;
        this.firstCol=0;
        ZUI.notify();
    }
    hasData(): boolean {
        if (this.dataTable) return true;
        return false;
    }
    protected colHeaderList(): string[] {
        if (this.dataTable){
            return this.dataTable.columnHeaders();
        } else {
            return null;
        }
    }

    protected rowLabelList(): string[] {
        if (this.dataTable){
            let rowLabels = this.dataTable.rowLabels();
            this.rowLabels= rowLabels;
        } else
            this.rowLabels= null;
        return this.rowLabels;
    }
    protected rowData(row: number, firstCol: number, lastCol: number): any[] {
        if (this.dataTable){
            let rowLabel = this.rowLabels[row];
            let ch = this.dataTable.columnHeaders();
            let rowData:any[]=[];
            for (let col=firstCol;col<=lastCol;col++){
                let colName=ch[col];
                let v = this.dataTable.get(rowLabel,colName);
                
                rowData.push(v);
            }
            return rowData;
        } else {
            return null;
        }
    }
    
}