/// <reference path="ZUI.ts"/>
/// <reference path="DivUI.ts"/>
type ObjZUI = {obj:DataObj, zui:ZUI};
class ObjListUI extends DivUI{
    protected objListF:()=>DataObj[];
    protected itemViewF:(obj:DataObj)=>ZUI;
    protected objMap:ObjZUI[];
    protected sortF:(o1:DataObj,o2:DataObj)=>number;
    constructor(objListF:()=>DataObj[]){
        super([]);
        this.objListF=objListF;
        this.objMap=[];
        this.style("ObjListUI");
    }
    itemView(itemViewF:(obj:DataObj)=>ZUI):ObjListUI{
        this.itemViewF=itemViewF;
        return this;
    }
    sort(sortF:(obj1:DataObj,obj2:DataObj)=>number):ObjListUI{
        this.sortF=sortF;
        return this;
    }
    protected buildKeyViewList(){
        let newObjMap:ObjZUI[]=[];
        let objList = this.objListF();
        if (this.sort){
            objList.sort(this.sortF);
        }
        this.items=[];
        for (let i in objList){
            let obj = objList[i];
            let viewZUI = this.findZUI(obj);
            if (!viewZUI){
                viewZUI = this.itemViewF(obj);
            }
            newObjMap.push({obj:obj,zui:viewZUI});
            this.items.push(viewZUI);
        }

        this.objMap = newObjMap;
    }
    protected findZUI(obj:DataObj):ZUI{
        for (let i in this.objMap){
            let map = this.objMap[i];
            if (map.obj === obj){
                return map.zui;
            }
        }
        return null;
    }
    renderJQ():JQuery{
        this.buildKeyViewList();
        return super.renderJQ();
    }

}