

abstract class DataColumnAbs {
    
    protected columnName:string;
    constructor(columnName:string){
        this.columnName=columnName;
    }
    abstract nValues():number;
    abstract get(row:number):string | number;

    name():string { return this.columnName}
    toJSON():(string|number)[]{
        let rslt:(string|number)[] = [];
        let nRows=this.nValues();
        for (let r=0;r<nRows;r++){
            rslt.push(this.get(r))
        }
        return rslt;
    }
    copy():DataColumn{
        let newDC = new DataColumn(this.name());
        let nRows = this.nValues();
        for (let row=0;row<nRows;row++){
            newDC.set(row,this.get(row));
        }
        return newDC;
    }
    sum():number{
        let total = 0;
        let nRows = this.nValues();
        for (let r=0;r<nRows;r++){
            let v = <number>this.get(r);
            if (!isNaN(v))
                total+=v;
        }
        return total;
    }
    sumSquares():number{
        let total = 0;
        let nRows = this.nValues();
        for (let r=0;r<nRows;r++){
            let v = <number>this.get(r);
            if (!isNaN(v))
                total+=v*v;
        }
        return total;
    }
    pairProduct(col2:DataColumn):DataColumn{
        let rslt=new DataColumn(this.columnName+"*"+col2.columnName);
        let nr = this.nValues();
        for (let r=0;r<nr;r++){
            let v1=<number>this.get(r);
            let v2=<number>col2.get(r);
            if (isNaN(v1) || isNaN(v2)){
                rslt.set(r,0);
            } else {
                rslt.set(r,v1*v2)
            }
        }
        return rslt;
    }
    dotProduct(col2:DataColumn):number {
        let rslt=0;
        let nr = this.nValues();
        for (let r=0;r<nr;r++){
            let v1=<number>this.get(r);
            let v2=<number>col2.get(r);
            if (!isNaN(v1) && !isNaN(v2)){
                rslt+=v1*v2
            }
        }
        return rslt;
    }
    count():number{
        let total=0;
        let nRows = this.nValues();
        for (let r=0;r<nRows;r++){
            let v=this.get(r);
            if (v || v==0)
                total++;
        }
        return total;
    }
    countNumbers():number{
        let total=0;
        let nRows = this.nValues();
        for (let r=0;r<nRows;r++){
            let v=<number>this.get(r);
            if (!isNaN(v))
                total++;
        }
        return total;
    }
    max():number{
        let max=<number>this.get(0);
        let nRows = this.nValues();
        for (let r=0;r<nRows;r++){
            let v=<number>this.get(r);
            if (v>max){
                max=v;
            }   
        }
        return max;
    }
    min():number{
        let min:number=<number>this.get(0)
        let nRows = this.nValues();
        for (let r=0;r<nRows;r++){
            let v=<number>this.get(r);
            if (!isNaN(v)){
                if (v<min){
                    min=v
                } 
            }   
        }
        return min;
    }
    
    db(msg?:string,maxRows=20){
        if (msg)
            DB.start(msg);
        let rows = this.nValues();
        let ellipses=false;
        if (rows>maxRows){
            rows=maxRows;
            ellipses=true;
        }
       
        for (let r=0;r<rows;r++){
            DB.msg(`[${r}]:"${this.get(r)}"`)
        }
        if (ellipses) DB.msg("...")
        if (msg)
            DB.end(msg)
    }
}