/// <reference path="DataSource.ts"/>



class CacheDataSource extends DataSource {
    actualSource:DataSource;
    static objLeaseTime:number = 1000*60*5; // five minutes
    static updateLatency:number = 500;
    
    constructor(actualSource:DataSource,dataMonitor?:DataMonitor){
        super();
        this.actualSource = actualSource;
        this.setDataMonitor(dataMonitor);
        this.recentCache={};
        this.prevCache={};
        this.recentFindCache={};
        this.prevFindCache={};
    }
    setDataMonitor(dataMonitor:DataMonitor){
        super.setDataMonitor(dataMonitor);
        this.actualSource.setDataMonitor(dataMonitor)
    }
    setRightsManager(rm:RightsManager){
        super.setRightsManager(rm);
        this.actualSource.setRightsManager(rm);
    }
    isRemote():boolean { return this.actualSource.isRemote();}
    GET(key:string, done:(err:string,data:DataObj)=>void,forceRequest:boolean,
            serverContext:ServerContext){
        let co:CacheObj = this.getCachedObj(key);
        if (co){
            if (co.requestInProgress){
                // wait and redo request
                setTimeout(()=>{
                    this.GET(key,done,forceRequest,serverContext);
                },1000)
                return;
            } else {
                if (!forceRequest && co.obj){
                    done(null,co.obj);
                    return;
                } else {
                    co.requestInProgress=true;
                }
            }
        } else {
            co = new CacheObj(null);
            co.requestInProgress=true;
            this.recentCache[key]=co;
        }
        this.actualSource.GET(key,(err:string,data:DataObj)=>{
            if (err){
                done(err,null);
                return;
            }
            
            data.dataSource=this;
            this.cacheObj(data);
            done(null,data);
            this.notify();
        },forceRequest,serverContext);
    }
    cGET(key:string,forceRequest?:boolean):DataObj{
        let cObj:CacheObj = this.getCachedObj(key);

        if (cObj){
            if (cObj.requestInProgress){
                return null;
            } else if (forceRequest){
                cObj.requestInProgress=true;
            } else {
                return cObj.obj;
            }
        }/*else {
            let co = new CacheObj(null);
            co.requestInProgress=true;
            this.recentCache[key]=co;
        }*/
        this.GET(key,()=>{},forceRequest,null);           // invoke get without waiting
        return null;
    }
    GETm(keys:string[],done:(err:string,data:DataObj[])=>void,forceRequest:boolean,
            serverContext:ServerContext){
        if (!keys || keys.length==0) {
            done(null,[]);
            return;
        }
        if (forceRequest){
            this.getMActual(keys,(err:string,data:DataObj[])=>{
                done(err,data);
            },forceRequest,serverContext)
        } else {
            if (this.cacheRequestInProgress(keys)){
                // another request already in progress
                // wait and reprocess request
                setTimeout(()=>{
                    this.GETm(keys,(subErr:string,subData:DataObj[])=>{
                        done(subErr,subData);
                    },forceRequest,serverContext)
                },1000)
            } else {
                let cObjs:DataObj[] = this.getCachedObjs(keys);
                if (cObjs){
                    done(null,cObjs);
                } else {
                    this.getMActual(keys,(err:string,data:DataObj[])=>{
                        done(err,data)
                    },forceRequest,serverContext)
                }
            }
        }
    }
    private getMActual(keys:string[],done:(err:string,data:DataObj[])=>void,
            forceRequest:boolean,serverContext:ServerContext){
        this.actualSource.GETm(keys,(err:string,data:DataObj[])=>{
            if (err){
                done(err,null);
                return;
            }
            
            for (let i in data)
                data[i].dataSource = this;
            this.cacheObjs(data);
            done(null,data);
        },forceRequest,serverContext);
    }
    cGETm(keys:string[],forceRequest?:boolean):DataObj[]{
        //DB.start("CacheDataSource.cGETm",keys);
        if (!keys || keys.length==0) {
            //DB.end("CacheDataSource.cGETm no keys")
            return [];
        }
        let cObjs:DataObj[] = this.getCachedObjs(keys);

        let remainingKeys:string[]=[];
        if (forceRequest){
            remainingKeys=keys;
        } else if (cObjs){
            for (let k in keys){
                let found=false;
                let key =keys[k];
                for (let o in cObjs){
                    let obj = cObjs[o];
                    if (obj && key==obj._key){
                        found=true;
                        break;
                    }
                }
                if (!found){
                    remainingKeys.push(key);
                }
            }
            if (remainingKeys.length>0){
                //DB.msg("CacheDataSource.cGETm getting somekeys",keys)
                this.GETm(remainingKeys,()=>{},forceRequest,null);  // force a get without waiting for response
            }
            //DB.end("CacheDataSource.cGETm GETm",cObjs)
            return cObjs;
        } else {
            //DB.msg("CacheDataSource.cGETm get all",keys);
            this.GETm(keys,()=>{},forceRequest,null);
            //DB.end("CacheDataSource.cGETm")
            return null;
        }
               
    }
    GETBlob(blobKey:string,serverContext:ServerContext,done:(err:string,blob:BlobStoreRec)=>void){

        this.actualSource.GETBlob(blobKey,serverContext,done);
    }
    PUT(obj:DataObj,done:(err:string,newObj:DataObj)=>void,serverContext:ServerContext){
        obj.dataSource = this;
        let typeCode = obj.getTypeCode()
        this.clearFindCache(typeCode);
        this.actualSource.PUT(obj,(err:string,putObj:DataObj)=>{
            this.clearFindCache(typeCode);
            if (putObj)
                this.cacheObj(putObj);
            //DB.msg("CacheDataSource PUT complete")
            done(err,putObj);
        },serverContext);
    }
    PUTm(objs:DataObj[],done:(err:string,newObjs:DataObj[])=>void, serverContext:ServerContext){
        for (let i in objs)
            objs[i].dataSource = this;
        if (objs && objs.length>0){
            let typeCode = objs[0].getTypeCode()
            this.clearFindCache(typeCode);
        }
        this.actualSource.PUTm(objs,done,serverContext);
    }
    PUTFileBlob(file:File,serverContext:ServerContext,done:(err:string,blobKey:string)=>void){
        this.actualSource.PUTFileBlob(file,serverContext,done);
    }
    PUTBlob(blob:Blob,name:string,serverContext:ServerContext,done:(err:string,blobKey:string)=>void){
        this.actualSource.PUTBlob(blob,name,serverContext,done);
    }
    DELETE(key:string,done?:(err:string)=>void){
        this.delObjCache(key);
        this.actualSource.DELETE(key,(delErr:string)=>{
            this.delObjCache(key);
            //DB.msg("CacheDataSource DELETE complete")
            if (done) done(delErr);
        });
    }
    FIND(typeCode:string, search:Query,done:(err:string,keys:string[],objs:DataObj[])=>void,
            keysOnly:boolean,forceRequest:boolean, serverContext:ServerContext){
        let findKeys:FindCacheObj = this.getCachedFind(typeCode,search);

        if (findKeys){
            if (findKeys.requestInProgress){
                setTimeout(()=>{
                    this.FIND(typeCode,search,done,keysOnly,false,serverContext);
                },1000);
                return;
            } else if (forceRequest){
                findKeys.requestInProgress=true;
                this.doFind(typeCode,search,done,keysOnly,serverContext);
            } else {
                if (findKeys.getKeys()){
                    if (keysOnly){
                        done(null,findKeys.getKeys(),[]);
                    } else {
                        this.GETm(findKeys.getKeys(),(getmErr:string,getMData:DataObj[])=>{
                            if (getMData){
                                done(null,findKeys.getKeys(),getMData)
                            } else {
                                done(getmErr,findKeys.getKeys(),[])
                            }
                        },false,serverContext)
                    }
                } 
            }
        } else {
            this.doFind(typeCode,search,done,keysOnly,serverContext);
        }
    }
    private doFind(typeCode:string,search:Query,done:(err:string,keys:string[],objs:DataObj[])=>void,
            keysOnly:boolean,serverContext:ServerContext){
        this.registerInProgress(typeCode,search);
        this.actualSource.FIND(typeCode,search,(err:string,keys:string[],objs:DataObj[])=>{
            if (err){
                done(err,null,null);
                return;
            }
            this.cacheFind(typeCode,search,keys,objs);
            done(err,keys,objs);
        },keysOnly,false,serverContext);
    }
    private registerInProgress(typeCode:string,search:Query){
        let newFindCache =new FindCacheObj(search,[]);
        newFindCache.requestInProgress=true;
        if (!this.recentFindCache[typeCode])
            this.recentFindCache[typeCode]=[];
        this.recentFindCache[typeCode].push(newFindCache)
    }
    cFIND(typeCode:string,search:Query,keysOnly:boolean,forceRequest:boolean):string[]{

        let findKeys:FindCacheObj = this.getCachedFind(typeCode,search);
        let rslt:string[] = [];
        if (findKeys){
            if (findKeys.requestInProgress){
                return findKeys.getKeys();
            } else if (forceRequest){
                this.FIND(typeCode,search,()=>{
                },keysOnly,forceRequest,null)
                return findKeys.getKeys();
            } else { // return what was in the cache
                
                rslt=findKeys.getKeys();
                return rslt;
            }
        } else { // nothing in the cache
            
            this.FIND(typeCode,search,()=>{
                
            },keysOnly,forceRequest,null)
            return null;
        }
    }
    touched(obj:DataObj):boolean{
        this.scheduleUpdate(obj);
        return true;
    }
    login(userId:string,password:string,done:(err:string)=>void,serverContext:ServerContext){
        this.actualSource.login(userId,password,done,serverContext);
    }
    httpLogin(userName:string, password:string,done:(err:string,user:DataObj)=>void){
        this.actualSource.httpLogin(userName,password,(aErr:string,aUser:DataObj)=>{
            if (aErr){
                done(aErr,null);
            } else {
                this.cacheObj(aUser);
                done(null,aUser);
            }
        });
    }
    createUser(userDesc:any,password:string,done:(err:string)=>void,serverContext:ServerContext){
        this.actualSource.createUser(userDesc,password,done,serverContext);
    }
     

    private MAX_CACHE=1000;
    private cacheCount=0;
    private recentCache:{ [key:string]:CacheObj};
    private prevCache: { [key:string]:CacheObj}
    private checkCacheSize(){
        this.cacheCount++;
        if (this.cacheCount>this.MAX_CACHE){
            this.cacheCount=0;
            this.prevCache=this.recentCache;
            this.recentCache={};
        }
    }

    private cacheObj(obj:DataObj){
        // this will put a single object in the cache and initiate notification.
        let key = obj._key;
        let recentObj = this.recentCache[key];
        let prevObj = this.prevCache[key];
        let now = Date.now();
        if (recentObj){
            this.recentCache[key]=new CacheObj(obj);
            obj.dataSource=this;
        } else if (prevObj){
            this.recentCache[key]=new CacheObj(obj);
            obj.dataSource=this;
            this.prevCache[key]=null;
            this.checkCacheSize();
        } else {
            this.recentCache[key]=new CacheObj(obj);
            obj.dataSource=this;
            this.checkCacheSize();
        }
        this.notify();
    }
    private cacheObjs(objs:DataObj[]){
        for (let i in objs){
            let obj = objs[i];
            this.cacheObj(obj);
        }
    }
    private getCachedObj(key:string):CacheObj{
        let recentObj = this.recentCache[key];
        let prevObj = this.prevCache[key];
        let now = Date.now();
        if (recentObj){
            if (recentObj.expired()){
                this.recentCache[key]=null;
            } else 
                return recentObj;
        } else if (prevObj){
            if (prevObj.expired()){
                this.prevCache[key]=null;
            } else 
                return prevObj;
        } else {
            this.checkCacheSize();
            return null;
        }
    }
    private cacheRequestInProgress(keys:string[]):boolean {
        for (let i in keys){
            let key=keys[i];
            let cache:CacheObj = this.getCachedObj(key);
            if (cache && cache.requestInProgress)
                return true;
        }
        return false;
    }
    private getCachedObjs(keys:string[]):DataObj[]{
        let rslt:DataObj[] = [];
        for (let i in keys){
            let key = keys[i];
            let obj:CacheObj = this.getCachedObj(key);
            if (obj && obj.obj){
                rslt.push(obj.obj);
            }
        }
        if (rslt.length==0)
            rslt=null;
        return rslt;
    }
    private delObjCache(key:string){
        let objTypeCode = key.split("_")[0];
        this.clearFindCache(objTypeCode);
        let recentObj = this.recentCache[key];
        if (recentObj)
            this.recentCache[key]=null;
        let prevObj = this.prevCache[key];
        if (prevObj)
            this.prevCache[key]=null;
        this.notify();
    }

    private MAX_FIND_CACHE=100;
    private findCacheCount=0;
    private recentFindCache:{ [typeCode:string]:FindCacheObj[] };
    private prevFindCache:{ [typeCode:string]:FindCacheObj[] };
    private checkFindCacheSize(){
        this.findCacheCount++;
        if (this.findCacheCount>this.MAX_FIND_CACHE){
            this.findCacheCount=0;
            this.prevFindCache = this.recentFindCache;
            this.recentFindCache={};
        }
    }
    private cacheFind(typeCode:string, search:Query,keys:string[],objs:DataObj[]){
        let recentSearch:FindCacheObj = this.locateFind(this.recentFindCache,typeCode,search);
        if (recentSearch){
            recentSearch.setData(search,keys);
            //DB.msg("cacheFind recentSearch",recentSearch);
        } else {
            let prevSearch:FindCacheObj= this.locateFind(this.prevFindCache,typeCode,search);
            if (prevSearch){
                prevSearch.setData(search,keys);
                if (!this.recentFindCache[typeCode])
                    this.recentFindCache[typeCode]=[];
                //DB.msg("cacheFind prevSearch",prevSearch);
                this.recentFindCache[typeCode].push(prevSearch);
                this.checkFindCacheSize();
            } else {
                let newSearch = new FindCacheObj(search,keys);
                if (!this.recentFindCache[typeCode])
                    this.recentFindCache[typeCode]=[];
                //DB.msg("cacheFind newSearch",newSearch)
                this.recentFindCache[typeCode].push(newSearch);
                this.checkFindCacheSize();
            }
        }
        this.cacheObjs(objs);
        this.notify();
    }
    private getCachedFind(typeCode:string,search:Query):FindCacheObj {
        let recentSearch:FindCacheObj = this.locateFind(this.recentFindCache,typeCode,search);
        if (recentSearch){
            if (recentSearch.expired()){
                this.purgeFind(this.recentFindCache,typeCode,search);
            } else 
                return recentSearch;
        } 
        let prevSearch:FindCacheObj= this.locateFind(this.prevFindCache,typeCode,search);
        if (prevSearch){
            if (prevSearch.expired()){
                this.purgeFind(this.prevFindCache,typeCode,search)
                
            } else {
                return prevSearch;
            }
        } 
        return null;
    
    }
    private locateFind(cache:{ [typeCode:string]:FindCacheObj[] },typeCode:string,search:Query):FindCacheObj{
        let searches:FindCacheObj[] = cache[typeCode];
        if (searches){
            for (let i in searches){
                let srch = searches[i];
                if (srch) {
                    if (Query.match(srch.search,search)){
                        return srch;
                    }
                }
            }
            return null;
        } else {
            return null;
        }
    }
    private purgeFind(cache:{ [typeCode:string]:FindCacheObj[] },typeCode:string,search:Query):void{
        let searches = cache[typeCode];
        if (searches){
            for (let i in searches){
                let srch:FindCacheObj = searches[i];
                if (srch) {
                    if (Query.match(srch.search,search)){
                        searches[i]=null;
                    }
                }
            }
            return null;
        } else {
            return null;
        }
    }
    private clearFindCache(objTypeCode:string){
        let possibleTypeCodes=this.superTypeCodes(objTypeCode);
        for (let c in possibleTypeCodes){
            let tc = possibleTypeCodes[c];
            this.recentFindCache[tc]=[];
            this.prevFindCache[tc]=[];
        }
    }
    private superTypeCodes(typeCode:string):string[]{
        let rslt:string[] = [];
        let classInfo = DataObj.classInfo;
        let cli = classInfo[typeCode];
        while (cli){
            rslt.push(typeCode);
            typeCode = cli.superCode;
            cli = classInfo[typeCode];
        }
        return rslt;
    }

    private pendingUpdates:{ [key:string]:DataObj };
    private pendingUpdateTimerStarted:boolean;
    private scheduleUpdate(obj:DataObj){
        if (!obj) return;
        if (!this.pendingUpdates)
            this.pendingUpdates={};
        this.pendingUpdates[obj._key]=obj;
        if (!this.pendingUpdateTimerStarted){
            this.pendingUpdateTimerStarted=null;
            setTimeout(()=>{
                let updates = this.pendingUpdates;
                this.pendingUpdates={};
                let updateList:DataObj[] = [];
                for (let key in updates){
                    let updateObj = updates[key];
                    updateList.push(updateObj);
                }
                this.PUTm(updateList,(err:string,newObjs:DataObj[])=>{
                    // nothing
                },null);
            },CacheDataSource.updateLatency);
        } 
    }
}
class CacheObj{
    timeCached:number;
    requestInProgress:boolean;
    obj:DataObj;
    constructor(obj:DataObj){
        this.timeCached=Date.now();
        this.requestInProgress = false;
        this.obj=obj;
    }
    expired():boolean{
        let now = Date.now();
        let time = now - this.timeCached;
        if (time>CacheDataSource.objLeaseTime)
            if (this.requestInProgress) {
                if (time > 2*CacheDataSource.objLeaseTime){
                    this.timeCached = now-CacheDataSource.objLeaseTime;
                    return true; // this will force a new request even if one is in progress
                } else {
                    return false;
                }
            } else {
                return true;
            }
        else
            return false;
    }
}
class FindCacheObj extends CacheObj{
    search:Query;
    private keys_:string[];
    private keyMap:{ [key:string]:boolean};
    setKeys(newKeys:string[]){
        this.keys_=newKeys;
        this.keyMap = {};
        for (let i in newKeys){
            let key = newKeys[i];
            this.keyMap[key]=true;
        }
    }
    getKeys():string[]{
        return this.keys_;
    }
    constructor(search:Query,keys:string[]){
        super(null);
        this.search = search;
        this.keys_=keys;
    }
    setData(search:Query,keys:string[]){
        this.timeCached=Date.now();
        this.requestInProgress = false;
        this.search = search;
        this.setKeys( keys);
    }

    checkDelete(key:string){
        if (this.keyMap[key]){
            this.keyMap[key]=false;
            this.rebuildKeysFromMap();
        }
    }
    checkForUpdate(obj:DataObj){
        let key = obj._key;
        if (key){
            if (this.keyMap[key]){
                if (this.search.matches(obj)){
                    return;
                } else {
                    this.keyMap[key]=false;
                    this.rebuildKeysFromMap();
                }
            } else {
                if (this.search.matches(obj)){
                    this.keyMap[key]=true;
                    this.rebuildKeysFromMap();
                } else
                    return;
            }
        }
    }
    private rebuildKeysFromMap(){
        this.keys_=[];
        for (let key in this.keyMap){
            if (this.keyMap[key])
                this.keys_.push(key);
        }
    }
}
