/**
* The DB module contains some basic debugging aids. In particular
* it provides mechanisms for writing outputs to the console log so that they appear in 
* a nested outline.
*/
module DB {
    function quiet():boolean{
        try {
            if (process.env.DB=="quiet")
                return true;
        } catch (e){
            return false;
        }
        return false;
    }
    var level = 0;
    /**
    * This will open a new level of indentation
    * @param message is the message to be output with the level header
    * @param obj an optional object that will be provided with the header.
    * In many browsers this object can be opened and explored in depth by the debugger.
    */
    export function start(message: string, obj?: any) {
        if (quiet()) return
        if (dbon) {
            msg(">" + message, obj);
            level++;
        }
    }
    /**
    * This will output a message at the current level of indentation
    * @param message is the message to be output 
    * @param obj an optional object that will be provided with the message.
    * In many browsers this object can be opened and explored in depth by the debugger.
    */
    export function end(message: string, obj?: any) {
        if (quiet()) return
        if (dbon) {
            level--;
            if (level < 0)
                level = 0;
            msg("<" + message, obj);
        }

    }
    var dbop:boolean=false;
    export function dbOnPage(db:boolean){
        dbop=db;
    }
    /**
    * This will close a level of indentation
    * @param message is the message to be output with the level footer
    * @param obj an optional object that will be provided with the footer.
    * In many browsers this object can be opened and explored in depth by the debugger.
    */
    export function msg(message: string, obj?: any) {
        if (quiet()) return
        if (dbon) {
            var pre = "";
            for (var i = 0; i < level; i++) {
                pre += "   ";
            }
            if (obj)
                console.log(pre + message, obj);
            else
                console.log(pre + message);
            if (dbop){
                let span = document.createElement("div");
                span.innerHTML=pre+message;
                span.style.cssText="width:100%";
                let dbout = document.getElementById("dbout");
                if (dbout)
                    dbout.append(span);
            }
        }
    }

    export function stackTrace(message: string) {
        if (quiet()) return
        try {
            throw new Error(message);
        } catch (e) {
            console.log(e);
        }
    }
    var dbon: boolean = true;
    /**
    * turns on debugging messages
    */
    export function on(msg: string) {
        dbon = true;
        start("+++" + msg);
    }
    /**
    * turns off debugging messages
    */
    export function off(msg: string) {
        end("---" + msg);
        dbon = false;
    }
    var dbCounts = {};
    export function count(name:string) {
    	let count = dbCounts[name];
    	if (count)
    		count++;
    	else
    		count=1;
    	dbCounts[name]=count;
    }
    export function showCount(name:string){
    	let count = dbCounts[name];
    	if (!count) count=0;
    	DB.msg(name,count);
    }
    export function showCounts(msg?:string){
    	let m:string = "counts ";
    	if (msg) m+=msg;
    	DB.start(m);
    	for (let name in dbCounts){
    		DB.showCount(name);
    	}
    	DB.end(m);
    }
    export function clearCounts(name?:string){
    	if (name){
    		dbCounts[name]=0;
    	} else {
    		for (let key in dbCounts){
    			DB.clearCounts(key);
    		}
    	}
    }
    var autoInterval=null;
    export function countsAutoShow(millis:number){
    	if (autoInterval)
    		autoInterval.clearInterval();
    	autoInterval = setInterval(()=>{
    		DB.showCounts();
    	},millis);
    }
    export function noCountsAutoShow(){
    	clearInterval(autoInterval);
    }

}