class UserManager {
    protected currentUserName:string;
    dataSource:DataSource;
    protected currentUserKey:string;
    constructor(){
        this.currentUserName=null;
        this.dataSource=null;
        this.currentUserKey=null;
    }
    get userName():string { return this.currentUserName}
    login(userName:string,password:string,done:(err:string,user:DataObj)=>void,
            serverContext:ServerContext){
        if (this.dataSource.isRemote()){
            this.dataSource.httpLogin(userName,password,(err:string,user:DataObj)=>{
                if (err) {
                    done(err,null);
                } else {
                    this.currentUserName=userName;
                    this.currentUserKey=user._key;
                    done(null,user);
                }
            });
        }else {
            this.currentUserName=userName;
            this.currentUserKey=null;
            done(null,null);
        }
    }

    logout(done:(err:string)=>void){
        if (this.dataSource.isRemote()){
            this.dataSource.httpLogout((err:string)=>{
                if (err) {
                    done(err);
                } else {
                    this.currentUserName=null;
                    this.currentUserKey=null;
                    done(null);
                }
            })
        } else {
            this.currentUserName=null;
            this.currentUserKey=null;
            done(null)
        }
    }
    createUser(userDesc:any,password:string,done:(err:string)=>void,
            serverContext:ServerContext){
        done(null);
    }
    getUserKey(serverContext?:ServerContext):string{
        if (serverContext){
            return serverContext.getUserKey();
        } else {
            return this.currentUserKey;
        }
    }
    getUser(done:(user:DataObj)=>void,serverContext?:ServerContext) {
        let userKey = this.getUserKey(serverContext);
        this.dataSource.GET(userKey,(err:string,user:DataObj)=>{
            done(user);
        },false,serverContext);
    }
    serverSideLogin(userName:string,password:string,serverContext:ServerContext,done:(err:string,user:DataObj)=>void){
        DB.msg("serverSideLogin not implemented for this ",this);
        done("no serverside login",null);
    }
    serverSideLogout(serverContext:ServerContext,done:(err:string)=>void){
        DB.msg("serverSideLogout not implemented for this ",this);
        done("no serverside logout");
    }
}