/// <reference path="ZUI.ts"/>

class FileDropTargetUI extends ZUI {
    onDrop:(files:File[])=>void;
    content:ZUI;
    hoverStyle_:string;
    id:string;
    constructor(content:ZUI){
        super();
        this.content=content;
        this.hoverStyle_="FileDropTargetUI-drop";
        this.id = this.uniqueId();
        PageManager.addAfterDOMNotice(()=>{
            let cnt = $("#"+this.id);
            cnt.on("dragenter dragover dragleave",(e)=>{
                e.preventDefault();
                e.stopPropagation();
                cnt.addClass(this.hoverStyle_);
            });
            cnt.on("dragleave",(e)=>{
                e.preventDefault();
                e.stopPropagation();
                cnt.removeClass(this.hoverStyle_);
            });
            cnt.on("drop",(e)=>{
                e.preventDefault();
                e.stopPropagation();
                let oe = <any>(e.originalEvent);
                let files = oe.dataTransfer.files;
                if (this.onDrop){
                    this.onDrop(files);
                }
            })
        });
    }
    drop(onDrop:(files:File[])=>void):FileDropTargetUI{
        this.onDrop=onDrop;
        return this;
    }
    hoverStyle(hStyle:string):FileDropTargetUI{
        this.hoverStyle_ = hStyle;
        return this;
    }

    renderJQ():JQuery{
        let jq = super.renderJQ();
        jq.attr("id",this.id);
        this.applyCSS(jq);
        return jq;
    }
}