/// <reference path="ZUI.ts"/>

type TypeSwitchUIChoice = {
    typeCode:string;
    zui:ZUI;
}
class TypeSwitchUI extends ZUI{
    choices: TypeSwitchUIChoice[];
    model:DataObj;
    constructor(){
        super();
        this.choices=[];
        this.model=null;
    }
    choice(typeCode:string, controller:ZUI):TypeSwitchUI{
        let choice:TypeSwitchUIChoice = {
            typeCode:typeCode,
            zui:controller
        }
        this.choices.push(choice);
        return this;
    }
    renderJQ():JQuery{
        let jq = $(`<div class='TypeSwitchUI ${this.classStr()}'></div>`);
        if (this.model && this.model instanceof DataObj){
            let tc = this.model.getTypeCode();
            for (let i in this.choices){
                let choice = this.choices[i];
                if (choice.typeCode == tc){
                    let choiceJQ = choice.zui.renderJQ();
                    jq.append(choiceJQ);
                    break;
                }
            }
        }
        this.applyCSS(jq);
        return jq;
    }
}