/// <reference path="ZUI.ts"/>

class ProcessLogUI extends ZUI{
    title:string;
    private log:string;
    private indent:number;
    private n:number;
    private outOf:number;
    private timingOn:boolean;
    private startTimeStack:number[];
    private lastOverWrite:boolean;
    constructor(){
        super();
        this.clear();
    }
    timing(timeOn=true):ProcessLogUI{
        this.timingOn=timeOn
        return this;
    }
    clear(){
        this.log="";
        this.indent=0;
        this.startTimeStack=[];
        this.lastOverWrite=false;
    }
    msg(msg:string){
        this.lastOverWrite=false;
        let theOut = "";
        for (let i=0;i<this.indent;i++){
            theOut+="  ";
        }
        theOut+=msg;
        if (this.timingOn){
            let date = new Date();
            let secs = date.getSeconds();
            let secStr = secs.toFixed(0);
            if (secStr.length<2)
                secStr="0"+secStr;
            theOut=`[${date.getMinutes()}:${secStr}] `+theOut;
        }
        this.log=theOut+"\n"+this.log;
        ZUI.notify();
    }
    overWrite(msg:string){
        if (this.lastOverWrite){
            let idx = this.log.indexOf("\n")
            this.log = this.log.substring(idx+1);
            this.msg(msg);
        } else {
            this.lastOverWrite=true;
            this.msg(msg);
        }
        this.lastOverWrite=true;
    }
    start(msg:string){
        this.msg("&gt;"+msg);
        this.indent++;
        let now =Date.now();
        this.startTimeStack.push(now);
    }
    end(msg:string){
        if (this.timingOn){
            let now = Date.now();
            let start = this.startTimeStack.pop();
            let sec = (now-start)/1000;
            msg+=` [${sec.toFixed(2)}sec]`
        }
        
        this.indent--;
        this.msg("&lt;"+msg);
    }
    renderJQ():JQuery{
        let jq= $(`<div class="ProcessLogUI ${this.classStr()}"></div>`)
        if (this.log && this.log.length>0){
            let btns=$(`<div style="float:right"></div>`)
            let clearBtn=$(`<button>clear</button>`)
            clearBtn.click(()=>{ 
                this.clear()
                ZUI.notify()
            })
            btns.append(clearBtn);
            jq.append(btns);
        }
        let log = $(`<pre class="ProcessLogUI-log"></pre>`);
        jq.append(log);
        log.html(this.log);
        this.applyCSS(jq);
        log.scrollTop(50);
        return jq
    }
}